import React from 'react';

import { SortableTable } from './SortableTable';

import { euroTeams } from './atoms';
import { useRecoilState } from 'recoil';

export default function AdminTable(props) {
    const { team } = props;

    const [teams] = useRecoilState(euroTeams);

    let teamData = [];
    let names = [];
    teams.forEach(item => {
        // if (item.shortName === team) teamData = item;
        item.tableData.forEach(player => {
            if (names.includes(player.name)) return;

            names.push(player.name);
            teamData.push(player);
        });
    });
    return (
        <div id="ShowTeam-InnerWrap">
            Adjust Stats
            <SortableTable
                team={team}
                type="display"
                products={teamData}
                isAdmin={true}
            />
        </div>
    );
}
