import React from 'react';
import ReactDOM from 'react-dom';
import * as serviceWorker from './serviceWorker';
import { HashRouter as Router, Switch, Route } from 'react-router-dom';

import AlertTemplate from 'react-alert-template-basic';
import { transitions, positions, Provider as AlertProvider } from 'react-alert';

import weatherApp from './reducers/';
import { Provider } from 'react-redux';
import { createStore } from 'redux';
import reducers from './reducers';
import { composeWithDevTools } from 'redux-devtools-extension';

import FrontPage from './Components/FrontPage';
import noSite from './Components/404';
import DiceGame from './Components/DiceGame';
import Weather from './Components/Weather';
import SuperHeroVersionTwo from './Components/SuperHeroSmackdown/VersionTwo';
import Recipes from './Components/Recipes';

import Euros from './Components/Euros/'

import { RecoilRoot } from 'recoil';

import Playoffs from './Components/Playoffs';

import Awards from './Components/ICHL';

import { Main } from './Components/BitCoinCalc';

// import CarouselDemo from './Components/Carousel';
import Carousel2Demo from './Components/CarouselMkII';

// import RDIU from './Components/ReactDragInUse';

import ContactManager from './Components/ContactManager';

import Favicon from 'react-favicon';

import icon from './img/favicon.png';

const composeEnhancers = composeWithDevTools({
    trace: true,
    traceLimit: 25,
});

export const store = createStore(weatherApp, composeEnhancers());

const options = {
    position: positions.TOP_CENTER,
    timeout: 2000,
    offset: '10px',
    transition: transitions.SCALE,
};

ReactDOM.render(
    <AlertProvider template={AlertTemplate} {...options}>
        <Provider store={createStore(reducers)}>
            <React.StrictMode>
                <Router basename="/">
                    <Favicon url={icon} />
                    <Switch>
                        <Route path="/" exact={true} component={FrontPage} />
                        <Route path="/home" component={FrontPage} />
                        <Route path="/dicegame" component={DiceGame} />
                        <Route path="/weather" component={Weather} />
                        <Route
                            path="/superherosmackdown"
                            component={SuperHeroVersionTwo}
                        />
                        <Route path="/recipes" component={Recipes} />
                        <Route path="/contact" component={ContactManager} />
                        <Route path="/carousel" component={Carousel2Demo} />
                        <Route path="/BitCoinCalc" component={Main} />
                        <RecoilRoot>
                            <Route path="/ICHL" component={Awards} />
                            <Route path="/Playoffs" component={Playoffs} />
                            <Route path="/Euros" component={Euros} />
                        </RecoilRoot>
                        <Route path="*" exact={true} component={noSite} />
                    </Switch>
                </Router>
            </React.StrictMode>
        </Provider>
    </AlertProvider>,
    document.getElementById('root')
);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.register();
