import React, { useEffect, useState } from 'react';

import styled from 'styled-components';

import { db } from '../ContactForm/firebase';

export default function AwardsPresentation() {
	const [aws, setAws] = useState(null);
	const [isNoms, setIsNoms] = useState(true);
	const [i, setI] = useState(0);

	function renderNoms() {
		console.log(aws[i].numNoms);
		const n = aws[i].numNoms;
		const award = aws[i];
		let noms = [];
		let descs = [];

		for (let x = 0; x < n; x++) {
			noms.push(award[`nomNum${x}`]);
			descs.push(award[`desc${x}`]);
		}
		return (
			<div>
				<h1 style={{ position: 'absolute', top: '0' }}>{aws[i].awardName}</h1>
				<h2>Nominees:</h2>
				{noms.map((item, ii) => {
					return <h3 key={ii}>{item}</h3>;
				})}
			</div>
		);
	}

	function renderWin() {
		return (
			<div>
				<h1 style={{ position: 'absolute', top: '0' }}>{aws[i].awardName}</h1>
			</div>
		);
	}

	useEffect(() => {
		if (aws) return;
		console.log('fetch awards');
		db.collection('awardstesttest')
			.get()
			.then(querySnapshot => {
				const data = querySnapshot.docs.map(doc => doc.data());
				console.log(data);
				setAws(data);
			});
	}, [aws]);

	return (
		<PresWrap>
			{aws && isNoms ? renderNoms() : aws && !isNoms ? renderWin() : null}
			<button
				onClick={() => {
					setIsNoms(!isNoms);

					if (!isNoms) setI(i + 1);
				}}
			>
				Next
			</button>
		</PresWrap>
	);
}

const PresWrap = styled.div`
	z-index: 99999999999999;
	height: 100vh;
	width: 100vw;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	background: radial-gradient(black, transparent);
	text-align: center;

	h1,
	h2 {
		color: white;
	}
	button {
		color: black;
		padding-left: 20px;
		padding-right: 20px;
		position: absolute;
		bottom: 0;
	}
`;
