import React, { useState, useEffect } from 'react';

import styled from 'styled-components';

import { useRecoilState } from 'recoil';
import { user } from './atoms';

export default function Lock() {
	const [pin, setPin] = useState('');

	const [, setUser] = useRecoilState(user);

	const updatePin = val => {
		if (pin.length === 4) return;
		setPin(`${pin}${val}`);
	};

	useEffect(() => {
		// localStorage.setItem('myCat', 'Tom');
		const team = localStorage.getItem('team');
		if (!team) return;
		setUser(team);
	});

	const hidePin = () => {
		let pinX = '';
		for (let x = 0; x < pin.length; x++) pinX = `${pinX}X`;
		return pinX;
	};

	useEffect(() => {
		if (pin.length === 4) {
			switch (pin) {
				case '9999':
					setUser('CBD');
					localStorage.setItem('team', 'CBD');

					break;
				case '9292':
					setUser('PEN');
					localStorage.setItem('team', 'PEN');

					break;
				case '8181':
					setUser('GLA');
					localStorage.setItem('team', 'GLA');

					break;
				case '3030':
					setUser('SUT');
					localStorage.setItem('team', 'SUT');

					break;
				default:
					setPin('');
					ref.current.className = 'shaker';
					window.setTimeout(() => {
						ref.current.className = '';
					}, 1000);
					break;
			}
		}
	}, [pin, setUser]);

	const ref = React.useRef(null);
	return (
		<LockWrap>
			<div ref={ref}>
				<h1>Enter Passcode</h1>
				<h4>
					<span style={{ visibility: 'hidden' }}>X</span>
					{hidePin()}
				</h4>

				<div className="digits">
					<div className="digit" onClick={() => updatePin(1)}>
						1
					</div>
					<div className="digit" onClick={() => updatePin(2)}>
						2
					</div>
					<div className="digit" onClick={() => updatePin(3)}>
						3
					</div>
					<div className="digit" onClick={() => updatePin(4)}>
						4
					</div>
					<div className="digit" onClick={() => updatePin(5)}>
						5
					</div>
					<div className="digit" onClick={() => updatePin(6)}>
						6
					</div>
					<div className="digit" onClick={() => updatePin(7)}>
						7
					</div>
					<div className="digit" onClick={() => updatePin(8)}>
						8
					</div>
					<div className="digit" onClick={() => updatePin(9)}>
						9
					</div>
					<div className="digit" onClick={() => updatePin(0)}>
						0
					</div>
				</div>
			</div>
		</LockWrap>
	);
}

const LockWrap = styled.div`
	* {
		color: white;
	}
	text-align: center;
	background: rbga(0, 0, 0, 0.6);
	height: 100vh;
	width: 100vw;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;

	.digits {
		display: flex;
		flex-wrap: wrap;

		.digit {
			width: 33%;
			height: 28%;
			background: #1a2d76;
			color: #c6d3f6;
			border-radius: 54%;
			pading: 10px;
			margin: auto;
			display: flex;
			align-items: center;
			justify-content: center;
			font-size: 29px;
			cursor: pointer;
			transition: all 400ms;

			&:hover {
				transition: all 400ms;
				opacity: 0.5;
			}
		}
	}
	.shaker {
		animation: shake 0.82s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;
		transform: translate3d(0, 0, 0);
		backface-visibility: hidden;
		perspective: 1000px;
	}
	@keyframes shake {
		10%,
		90% {
			transform: translate3d(-1px, 0, 0);
		}

		20%,
		80% {
			transform: translate3d(2px, 0, 0);
		}

		30%,
		50%,
		70% {
			transform: translate3d(-4px, 0, 0);
		}

		40%,
		60% {
			transform: translate3d(4px, 0, 0);
		}
	}
`;
