import React from 'react';

import styled from 'styled-components';

export default function Button(props) {
	const maxWidth = props.maxWidth || '15rem';

	function contact() {}

	return (
		<ButtonS maxWidth={maxWidth} onClick={props.onClick || contact}>
			{props.label}
		</ButtonS>
	);
}

const desktop = `@media only screen and (min-width: 768px) `;

const ButtonS = styled.button`
	max-width: ${props => props.maxWidth};
	min-width: 13rem;
	z-index: 40;
	text-transform: capitalize;
	background: dodgerblue;
	border-radius: 10rem;
	border: 2px solid black;
	cursor: pointer;
	font: inherit;
	outline: none;
	transition: all ease 400ms;
	font-size: 1.25rem;
	width: 70%;
	padding: 20px 32px;
	color: #191a1d;

	${desktop} {
		padding: 1.6rem 2.4rem;
		width: 80%;
	}

	&:hover {
		transform: scale(1.09);
		background: none;
		color: white;
		border: 2px solid dodgerblue;
		transition: all ease 400ms;
	}
`;
