import React from 'react';

import TeamCard from './TeamCard';

import { useRecoilState } from 'recoil';
import { playerTeamsCalc, KuchPoints } from './atoms';

import { cloneDeep } from 'lodash';

export default function BoxScore() {
    const [scores] = useRecoilState(playerTeamsCalc);
    const [KP] = useRecoilState(KuchPoints);
    let newScores = [
        cloneDeep(scores.CBD),
        cloneDeep(scores.PEN),
        cloneDeep(scores.GLA),
        cloneDeep(scores.SUT),
    ];
    newScores[0].teamName = 'CBD';
    newScores[1].teamName = 'PEN';
    newScores[2].teamName = 'GLA';
    newScores[3].teamName = 'SUT';
    newScores[3].FP = newScores[3].FP + KP;

    newScores.sort((a, b) => a.FP + a.BP - (b.FP + b.BP));
    newScores.reverse();
    // console.log({ newScores });

    return (
        <div className="box-score">
            <div style={{ background: 'transparent' }}>
                <h3>
                    1st{' '}
                    <span role="img" aria-label="place">
                        🥇
                    </span>
                </h3>
                <TeamCard
                    canClick={true}
                    noMargin={true}
                    team={newScores[0].teamName}
                />
            </div>
            <div style={{ background: 'transparent' }}>
                <h3>
                    2nd
                    <span role="img" aria-label="place">
                        🥈
                    </span>
                </h3>
                <TeamCard
                    canClick={true}
                    noMargin={true}
                    team={newScores[1].teamName}
                />
            </div>
            <div style={{ background: 'transparent' }}>
                <h3>
                    3rd
                    <span role="img" aria-label="place">
                        🥉
                    </span>
                </h3>
                <TeamCard
                    canClick={true}
                    noMargin={true}
                    team={newScores[2].teamName}
                />
            </div>
            <div style={{ background: 'transparent' }}>
                <h3>
                    4th
                    <span role="img" aria-label="place">
                        🍀
                    </span>
                </h3>
                <TeamCard
                    canClick={true}
                    noMargin={true}
                    team={newScores[3].teamName}
                />
            </div>
        </div>
    );
}
