import React, { useEffect, useState } from 'react';

import axios from 'axios';

import { SortableTable } from './SortableTable';

import { useRecoilState } from 'recoil';
import { teamSelectedInDropDown } from './atoms';

import styled from 'styled-components';

import { useAlert } from 'react-alert';

export default function DisplayTeam(props) {
    const { type, displayTeam } = props;

    const alert = useAlert();

    const [teamToShow] = useRecoilState(teamSelectedInDropDown);

    // const teamInfoAPI = 'https://statsapi.web.nhl.com/api/v1/teams';
    // const teamInfoAPI = 'https://statsapi.web.nhl.com/api/v1/teams';
    const teamAPI = `https://statsapi.web.nhl.com/api/v1/teams/${teamToShow[1]}?expand=team.roster&season=20142015`;
    // const API = `https://statsapi.web.nhl.com/api/v1/teams/${teamToShow[1]}/stats`;
    const [roster, setRoster] = useState(null);

    const [data, setData] = useState([]);

    // console.log('🚨🚨🚨', data)

    useEffect(() => {
        // console.log('🥶🥶🥶')
        if (!teamToShow && type !== 'display') return;

        axios
            .get(teamAPI)
            .then(res => {
                // setTeamsInfo(res.data.teams.sort((a, b) => (a.name > b.name ? 1 : -1)));
                // console.log(res.data.teams[0].roster);
                // console.log({ res });
                setRoster(res.data.teams[0].roster);
            })
            .then(() => {})
            .catch(err => {
                // console.log('✅', err);
                alert.show(`Error getting team ${err}`, {
                    timeout: 2000,
                });
            });
    }, [alert, teamAPI, teamToShow, type]);

    const [canDisplay, setCanDisplay] = useState(false);

    useEffect(() => {
        // console.log('🤖')
        if (!roster) return;

        let indStats = {};
        let data = [];

        roster.roster.forEach((item, i) => {
            const playerApi = `https://statsapi.web.nhl.com${item.person.link}/stats?stats=statsSingleSeason`;
            axios
                .get(playerApi)
                .then(res => {
                    if (
                        res.data.stats[0].splits[0] &&
                        res.data.stats[0].splits[0].length !== 0
                    ) {
                        const stats = res.data.stats[0].splits[0].stat;
                        let player = {
                            id: item.person.id,
                            number: item.jerseyNumber || 0,
                            name: item.person.fullName || 'No Name',
                            pos: item.position.code || 'Fan',
                            goals: stats.goals || null,
                            shg: stats.shortHandedGoals || null,
                            assists: stats.assists || null,
                            gp: stats.gamesStarted || stats.games,
                            ppp: stats.powerPlayPoints || null,
                            ga: stats.goalsAgainst || null,
                            so: stats.shutouts || null,
                            wins: stats.wins || null,
                            details: item,
                        };
                        if (player.pos === 'G') {
                            // console.log('from TABLE', { player });
                            player.fantasy =
                                player.so * 2 +
                                player.ga * -0.25 +
                                player.gp * 2 +
                                player.wins * 2;
                        } else
                            player.fantasy =
                                player.goals * 3 +
                                player.assists * 2 +
                                player.ppp +
                                player.shg * 2;

                        player.avg = player.fantasy / player.gp;
                        player.avg = Math.round(player.avg * 10) / 10;
                        player.id = i + 1;
                        // console.log(player.id);
                        data.push(player);

                        indStats[item.person.id] = stats;
                        if (i === roster.roster.length - 1) {
                            setData(data);
                            // setCanDisplay(true);
                        }
                    } else {
                        // setCanDisplay(true);

                        setData(data);
                        let player = {
                            id: item.person.id,
                            number: item.jerseyNumber || 0,
                            name: item.person.fullName || 'No Name',
                            pos: item.position.code || 'Fan',
                            goals: 0,
                            shg: 0,
                            assists: 0,
                            gp: 0,
                            ppp: 0,
                            ga: 0,
                            so: 0,
                            wins: 0,
                            fantasy: 0,
                            avg: 0,
                        };

                        // console.log(player.id);
                        data.push(player);
                    }
                })
                .then(() => {
                    if (data.length === roster.roster.length)
                        setCanDisplay(true);
                })

                .catch(err => {
                    alert.show(`Error getting team, try again ${err}`, {
                        timeout: 2000,
                        top: 100,
                    });
                    indStats[item.person.id] = { error: true };
                });
        });
    }, [alert, roster]);

    // console.log(data[2]);

    return (
        <DisTeamWrap>
            <h2>{type === 'add' ? teamToShow[0] : displayTeam}</h2>
            {canDisplay ? <SortableTable type="add" products={data} /> : null}
        </DisTeamWrap>
    );
}

const DisTeamWrap = styled.div`
    .player-div {
        display: flex;
        background: #fafafa;
        align-items: center;
        border: 1px solid black;
        border-radius: 0;
        margin: 0;

        .name-details {
            width: 33%;
            border-radius: 0;
            border-right: 2px solid black;
            display: flex;
            margin: 0;
            padding: 0;
            background: #fafafa;
            display: flex;
            justify-content: space-between;
        }

        .stat-details {
            display: flex;
            align-items: center;
            padding: 0;
            margin: 0;
            padding-left: 10px;
            background: #fafafa;
            width: 33%;
            justify-content: space-between;
        }

        * {
            margin-right: 10px;
        }
    }
`;
