import React from 'react';

import { useSelector } from 'react-redux';

import ContactForm from '../ContactForm';
import ContactFormError from '../ContactFormError';
import ContactFormThank from '../ContactFormThank';

export default function ContactManager() {
	document.title = 'Rick Gove | Contact Me';
	const { name, email, showError, error, showThank } = useSelector(
		state => state.contactData
	);

	if (!error && !showError && !showThank) {
		return <ContactForm />;
	}
	if (error && showError) {
		return <ContactFormError name={name} error={error} />;
	}
	if (showThank) {
		return <ContactFormThank name={name} email={email} />;
	}
}
