import React, { useState, useRef, useEffect } from 'react';

import { useDispatch, useSelector } from 'react-redux';

import styled from 'styled-components';

import GoHome from '../GoHome';

import { db } from './firebase';

import _ from 'lodash';

import { setContactData } from '../../actions';

export default function ContactForm() {
	const dispatch = useDispatch();

	let contactData = _.cloneDeep(useSelector(state => state.contactData));

	const headingRef = useRef();

	const formRef = useRef();

	const [nameVal, setNameVal] = useState('');
	const [nameRefFocus, setNameRefFocus] = useState(false);
	const nameRef = useRef();
	const labelNameRef = useRef();

	const [errors, setErrors] = useState({});

	const [emailVal, setEmailVal] = useState('');
	const [emailRefFocus, setEmailRefFocus] = useState(false);
	const emailRef = useRef();
	const labelEmailRef = useRef();

	const [messageVal, setMessageVal] = useState('');
	const [messageRefFocus, setMessageRefFocus] = useState(false);
	const messageRef = useRef();
	const labelMessageRef = useRef();

	function submitToRedux() {
		dispatch(setContactData(contactData));
	}

	function submitToFirebase() {
		contactData.showThank = true;
		contactData.name = nameVal;
		contactData.message = messageVal;
		contactData.email = emailVal;
		db.collection('Messages')
			.add({
				name: nameVal,
				email: emailVal,
				message: messageVal,
			})
			.then(() => {
				contactData.showThank = true;
			})
			.catch(err => {
				contactData.error = err.message;
				contactData.showError = true;
			});
		submitToRedux();
	}

	// Color all errors:
	useEffect(() => {
		if (errors.name) {
			labelNameRef.current.style.color = '#ce4e6c';
		}

		if (!messageRefFocus && !errors.message) {
			labelMessageRef.current.style.color = 'grey';
		}
		if (errors.email) {
			labelEmailRef.current.style.color = '#ce4e6c';
		}
	}, [errors, messageRefFocus]);

	function validateName() {
		if (nameVal === '') {
			nameRef.current.placeholder = 'Input a name';
			nameRef.current.style.borderBottom = '2px solid #ce4e6c';
			labelNameRef.current.className = 'label-name-above error';
			labelNameRef.current.style.color = '#ce4e6c';
			setErrors(v => ({ ...v, name: true }));
		} else {
			nameRef.current.style.borderBottom = '2px solid grey';
			labelNameRef.current.className = 'label-name-above';
			labelNameRef.current.style.color = 'dodgerblue';
			setErrors(v => ({ ...v, name: false }));
		}
	}

	function validateEmail() {
		function validateEmailRegEx(email) {
			const re = /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/; // eslint-disable-line no-useless-escape
			return re.test(email);
		}
		if (!validateEmailRegEx(emailVal)) {
			setErrors(v => ({ ...v, email: true }));
			emailRef.current.style.borderBottom = '2px solid #ce4e6c';
			labelEmailRef.current.className = 'label-name-above';
			labelEmailRef.current.style.color = '#ce4e6c';
		} else {
			setErrors(v => ({ ...v, email: false }));
			emailRef.current.style.borderBottom = '2px solid grey';
		}
	}

	function validateMessage() {
		if (messageVal === '') {
			messageRef.current.style.border = '2px solid #ce4e6c';
			labelMessageRef.current.className = 'label-message-above';
			labelMessageRef.current.style.color = '#ce4e6c';
			setErrors(v => ({ ...v, message: true }));
		} else {
			messageRef.current.style.border = '2px solid grey';
			labelMessageRef.current.className = 'label-message-above';
			labelMessageRef.current.style.color = 'dodgerblue';
			setErrors(v => ({ ...v, message: false }));
		}
	}
	function allInputsValid() {
		validateName();
		validateEmail();
		validateMessage();
		if (
			errors.name === false &&
			errors.email === false &&
			errors.message === false
		) {
			headingRef.current.style.opacity = 0;
			formRef.current.style.opacity = 0;
			submitToRedux();
			submitToFirebase();
		}
	}

	function handleEnter(k) {
		if (k.code === 'Enter') {
			if (!messageRefFocus) allInputsValid();
		}
	}
	useEffect(() => {
		document.addEventListener('keypress', handleEnter);
		return function () {
			document.removeEventListener('keypress', handleEnter);
		};
	});
	return (
		<>
			<meta
				name="viewport"
				content="width=device-width, initial-scale=1.0, maximum-scale=1.0, user-scalable=0"
			/>
			<GoHome />
			<CF>
				<h1 ref={headingRef}>Contact Me</h1>
				<div
					ref={formRef}
					id="contact-form"
					onSubmit={e => {
						e.preventDefault();
					}}
				>
					{/*  NAME */}
					<input
						placeholder="Name"
						onFocus={() => {
							setNameRefFocus(true);
							nameRef.current.placeholder = '';
							if (errors.name) {
								labelNameRef.current.color = '#ce4e6c';
							} else {
								labelNameRef.current.style.color = 'dodgerblue';
							}
						}}
						onBlur={() => {
							setNameRefFocus(false);
							validateName();
							if (errors.name || nameRef?.current.value === '') {
								labelNameRef.current.color = '#ce4e6c';
							} else {
								labelNameRef.current.style.color = 'grey';
							}
							if (nameVal === '') {
								window.setTimeout(() => {
									labelNameRef.current.style.color = '#ce4e6c';
									nameRef.current.placeholder = 'Name';
								}, 399);
							}
						}}
						ref={nameRef}
						className="input-name"
						autoComplete="off"
						type="text"
						id="name"
						name="name"
						onChange={() => {
							setNameVal(nameRef.current.value);
						}}
						value={nameVal}
					/>
					<label
						ref={labelNameRef}
						className={
							nameVal !== '' || nameRefFocus || errors.name
								? 'label-name-above'
								: 'label-name'
						}
						htmlFor="name"
						style={
							!errors.name && !nameRefFocus
								? { color: 'grey' }
								: { color: 'dodgerblue' }
						}
					>
						{errors.name ? 'Name *' : 'Name'}
					</label>
					{/* email */}
					<input
						ref={emailRef}
						placeholder="Email"
						onFocus={() => {
							setEmailRefFocus(true);
							if (errors.email || emailRef?.current.value === '') {
								labelEmailRef.current.color = '#ce4e6c';
							} else {
								labelEmailRef.current.style.color = 'dodgerblue';
							}
						}}
						onBlur={() => {
							validateEmail();
							setEmailRefFocus(false);
							if (errors.email || emailRef?.current.value === '') {
								labelEmailRef.current.color = '#ce4e6c';
							} else {
								labelEmailRef.current.style.color = 'grey';
							}

							if (emailVal === '') {
								window.setTimeout(() => {}, 399);
							}
						}}
						className="input-email"
						autoComplete="off"
						type="email"
						id="email"
						name="email"
						onChange={() => {
							setEmailVal(emailRef.current.value);
						}}
						value={emailVal}
					/>
					<label
						ref={labelEmailRef}
						className={
							emailVal !== '' || emailRefFocus || errors.email
								? 'label-name-above'
								: 'label-name'
						}
						htmlFor="email"
						style={
							!errors.email && !emailRefFocus
								? { color: 'grey' }
								: { color: 'dodgerblue' }
						}
					>
						{errors.email ? 'Email *' : 'Email'}
					</label>
					<textarea
						onFocus={() => {
							setMessageRefFocus(true);
							if (errors.message) {
								labelMessageRef.current.style.color = '#ce4e6c';
							} else {
								labelMessageRef.current.style.color = 'dodgerblue';
							}
							messageRef.current.placeholder = '';
						}}
						onBlur={() => {
							setMessageRefFocus(false);
							validateMessage();
							if (messageVal !== '') {
								labelMessageRef.current.style.color = 'grey';
							} else {
								labelMessageRef.current.style.color = '#ce4e6c';
							}
							if (messageVal === '') {
								window.setTimeout(() => {
									messageRef.current.placeholder = 'Your Message';
								}, 399);
							}
						}}
						value={messageVal}
						ref={messageRef}
						className="message"
						placeholder="Your Message"
						autoComplete="off"
						type="text"
						id="message"
						name="message"
						resize="false"
						onChange={() => {
							setMessageVal(messageRef.current.value);
						}}
					/>
					<label
						className={
							messageVal !== '' || messageRefFocus || errors.message
								? 'label-message-above'
								: 'label-message'
						}
						ref={labelMessageRef}
						htmlFor="message"
					>
						{!errors.message ? 'Your Message' : 'Your Message *'}
					</label>
					<button onClick={allInputsValid}>Submit</button>
				</div>
			</CF>
		</>
	);
}

export const CF = styled.div`
	* {
		font-family: 'Rubik', sans-serif;
		max-width: 70vw;
	}

	h1 {
		text-align: center;
		transition: 'all 1s';
	}

	align-items: center;
	justify-content: center;
	background: black;
	color: dodgerblue;
	min-height: 100vh;
	display: flex;
	flex-direction: column;

	#contact-form {
		transition: all 1s;
		display: flex;
		flex-direction: column;
	}

	input:-webkit-autofill,
	input:-webkit-autofill:hover,
	input:-webkit-autofill:focus,
	textarea:-webkit-autofill,
	textarea:-webkit-autofill:hover,
	textarea:-webkit-autofill:focus,
	select:-webkit-autofill,
	select:-webkit-autofill:hover,
	select:-webkit-autofill:focus {
		-webkit-text-fill-color: white;
		-webkit-box-shadow: 0 0 0px 1000px #000 inset;
		transition: background-color 5000s ease-in-out 0s;
	}

	h1 {
		font-size: 4rem;
		letter-spacing: 10px;
		color: dodgerblue;
		margin-bottom: 3rem;

		@media (max-width: 600px) {
			font-size: 3rem;
		}
		@media (max-width: 375px) {
			font-size: 2rem;
		}
	}

	form {
		display: flex;
		flex-direction: column;
	}

	.label-name {
		color: white;
		opacity: 0;
		padding-top: 2rem;
		position: relative;
		top: -65px;
		font-size: 1.4rem;
		transition: all 400ms;
		opcaity: 0;
	}

	.label-name-above {
		position: relative;
		padding-top: 2rem;
		color: dodgerblue;
		top: -75px;
		transition: all 400ms;
		opacity: 1;
		font-size: 1.4rem;
	}

	.input-name {
		width: 26rem;
		background: black;
		color: white;
		outline: none;
		border: none;
		border-bottom: 2px solid grey;

		&:focus {
			border-bottom: 2px solid dodgerblue;
		}
	}
	.input-email {
		width: 26rem;
		background: black;
		color: white;
		outline: none;
		border: none;
		border-bottom: 2px solid grey;

		&:focus {
			border-bottom: 2px solid dodgerblue;
		}
	}

	.message {
		border: 2px solid grey;
		height: 6rem;
		width: 26rem;
		max-width: 65vw;
		background: black;
		outline: none;
		color: white;
		resize: none;
		padding: 5px;
		&:focus {
			border: 2px solid dodgerblue;
		}
	}

	.label-message {
		color: white;
		opacity: 0;
		padding-top: 2rem;
		position: relative;
		top: -141px;
		font-size: 1.4rem;
		// left: 10px;
		transition: all 400ms;
		opcaity: 0;
	}

	.label-message-above {
		position: relative;
		padding-top: 2rem;
		color: #ce4e6c;
		top: -170px;
		// left: 10px;
		transition: all 400ms;
		opacity: 1;
		font-size: 1.4rem;
	}

	.error {
		color: #ce4e6c;
	}

	.error-span-name {
		display: inline;
	}

	button {
		border: 4px solid dodgerblue;
		font: inherit;
		color: black;
		background: dodgerblue;
		padding: 1rem 0.4rem;
		outline: none;
		transition: all 400ms;
		cursor: pointer;
		font-size: 1.4rem;
		border-radius: 100px;
		margin-bottom: 2rem;
		z-index: 10;

		&:hover {
			cursor: pointer;
			transform: scale(1.1);
			background: black;
			transition: all 400ms;
			color: dodgerblue;
		}
	}

	.thank-you,
	.error-div {
		opacity: 1;
		font-align: center;
		height: 100vh;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		z-index: -1;
		transition: all 4s;
		width: 100%;
	}
`;
