import React, { useRef, useState, useEffect } from 'react';

import styled from 'styled-components';

const CarouselDiv = styled.div`
	display: grid;
	grid-template-columns: repeat(50, 20rem);
	height: 20rem;
	overflow-y: scroll;
	scrollbar-width: none;
	scroll-behavior: smooth;

	scroll-snap-type: x mandatory;

	cursor: grab;

	.grabbing {
		cursor: grabbing;
	}

	&::-webkit-scrollbar {
		display: none;
	}

	-ms-overflow-style: none;
	scrollbar-width: none;
	-webkit-overflow-scrolling: touch;
`;

const Slide = styled.div`
	height: 20rem;
	width: 20rem;
	display: flex;
	align-items: center;
	justify-content: center;
	scroll-snap-align: start;
`;

const Border = styled.div`
	height: 20rem;
	width: 20rem;
	overflow: hidden;
	position: absolute;

	.arrows {
		position: relative;
		display: flex;
		top: -13rem;
		justify-content: space-between;
		-webkit-touch-callout: none;
		-webkit-user-select: none;
		-khtml-user-select: none;
		-moz-user-select: none;
		-ms-user-select: none;
		user-select: none;
	}

	.arrow {
		opacity: 0.7;
		background: grey;
		display: flex;
		align-items: center;
		justify-content: center;
		position: relative;
		padding: 0.5rem;
		cursor: pointer;
		transition: all 400ms;

		&:hover {
			opacity: 1;
			transition: all 400ms;
		}
	}

	.prev {
		top: -14rem;
		left: -9rem;
	}
`;

const Dots = styled.div`
	position: relative;
	z-index: 100;
	display: flex;
	opacity: 0.4;
	align-items: center;
	width: 100%;
	justify-content: center;
	bottom: 2rem;
	cursor: pointer;
	font-size: 0.8rem;

	outline: none;

	-webkit-touch-callout: none;
	-webkit-user-select: none;
	-khtml-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;

	transition: all 400ms;

	&:hover {
		opacity: 1;
		transition: all 400ms;
	}
`;

const Carousel = props => {
	// Props
	const kids = props.children;

	// Refs
	const carouselRef = useRef(null);
	const slideRef = useRef(null);

	// State
	const [active, setActive] = useState(0);
	const [determinedFrameSize, setDeterminedFrameSize] = useState(null);
	const [initClick, setInitClick] = useState(null);
	const [isDown, setIsDown] = useState(false);

	// Determine Slide Size
	useEffect(() => {
		if (!slideRef) return;
		setDeterminedFrameSize(slideRef.current.clientWidth);
	}, [determinedFrameSize]);

	// setActiveBasedOnScroll
	const determineActive = () => {
		if (!carouselRef) return;

		setActive(Math.floor(carouselRef.current.scrollLeft / determinedFrameSize));
	};

	//////////////////////////////////
	// set body style
	//
	const style = document.createElement('style');
	style.innerHTML = `body::-webkit-scrollbar {display: none;}`;
	document.head.appendChild(style);

	document.body.style.overscrollBehaviorX = 'none';

	//
	//
	// End body style
	//////////////////////////////////////

	///////////////////////////////
	// Click and Drag:
	//
	const handleMouseDown = e => {
		e.preventDefault();

		carouselRef.current.style.cursor = 'grabbing';

		setIsDown(true);

		setInitClick(e.clientX - determinedFrameSize + 14);
	};

	document.onmouseup = e => {
		if (!isDown) return;
		determineActive();
		setIsDown(false);
		carouselRef.current.style.cursor = 'grab';
	};

	const handleMouseMove = e => {
		if (!isDown) return;

		const drag = initClick - (e.clientX - determinedFrameSize + 14);

		carouselRef.current.scrollBy({
			left: drag,
			behavior: 'smooth',
		});
	};

	document.onmousemove = e => handleMouseMove(e);
	//
	// End Click and Drag
	///////////////////////////

	const renderDots = () => {
		return kids.map((item, i) => {
			return (
				<span
					key={i}
					role="img"
					aria-label="dot"
					onClick={() => {
						carouselRef.current.scrollTo({
							left: i * determinedFrameSize,
							behavior: 'smooth',
						});
					}}
				>
					{active === i ? '🔘' : '⚪️'}
				</span>
			);
		});
	};

	const prev = () => {
		if (active === 0) return;

		carouselRef.current.scrollBy({
			left: -determinedFrameSize,
			behavior: 'smooth',
		});
	};

	const next = () => {
		if (active === kids.length - 1) return;

		carouselRef.current.scrollBy({
			left: determinedFrameSize,
			behavior: 'smooth',
		});
	};

	const renderKids = () => {
		return kids.map((item, i) => {
			return (
				<Slide ref={slideRef} id={`Slide-${i}`} key={i}>
					{item}
				</Slide>
			);
		});
	};

	return (
		<>
			<Border id="Border">
				<CarouselDiv
					onMouseDown={handleMouseDown}
					onScroll={determineActive}
					ref={carouselRef}
					id="CarouselDiv"
				>
					{renderKids()}
				</CarouselDiv>
				<Dots>{renderDots()}</Dots>
				<div className="arrows">
					{active === 0 ? (
						<span></span>
					) : (
						<div onClick={prev} className="arrow">
							{'<'}
						</div>
					)}
					{active === kids.length - 1 ? (
						<span></span>
					) : (
						<div onClick={next} className="arrow">
							{'>'}
						</div>
					)}
				</div>
			</Border>
		</>
	);
};

export default Carousel;
