import React, { useRef, useState, useEffect } from 'react';

import styled from 'styled-components';

const CarouselDiv = styled.div`
	display: grid;
	grid-template-columns: repeat(50, 80vw);
	height: 80vh;
	overflow-y: scroll;
	scrollbar-width: none;
	scroll-behavior: smooth;

	scroll-snap-type: x mandatory;

	.grabbing {
		cursor: grabbing;
	}

	&::-webkit-scrollbar {
		display: none;
	}

	-ms-overflow-style: none;
	scrollbar-width: none;
	-webkit-overflow-scrolling: touch;
`;

const Slide = styled.div`
	height: 80vh;
	width: 80vw;
	display: flex;
	align-items: center;
	justify-content: center;
	scroll-snap-align: start;
`;

const Border = styled.div`
	height: 80vh;
	width: 80vw;
	overflow: hidden;
	position: absolute;

	.arrows {
		position: relative;
		display: flex;
		top: -13rem;
		justify-content: space-between;
		-webkit-touch-callout: none;
		-webkit-user-select: none;
		-khtml-user-select: none;
		-moz-user-select: none;
		-ms-user-select: none;
		user-select: none;
	}

	.arrow {
		opacity: 0.7;
		background: grey;
		display: flex;
		align-items: center;
		justify-content: center;
		position: relative;
		padding: 0.5rem;
		cursor: pointer;
		transition: all 400ms;

		&:hover {
			opacity: 1;
			transition: all 400ms;
		}
	}

	.prev {
		top: -14rem;
		left: -9rem;
	}
`;

const Carousel = props => {
	// Props
	const kids = props.children;

	// Refs
	const carouselRef = useRef(null);
	const slideRef = useRef(null);

	// State
	const [active] = useState(0);
	const [determinedFrameSize, setDeterminedFrameSize] = useState(null);
	const [initClick] = useState(null);
	const [isDown] = useState(false);

	// Determine Slide Size
	useEffect(() => {
		if (!slideRef) return;
		setDeterminedFrameSize(slideRef.current.clientWidth);
	}, [determinedFrameSize]);

	// setActiveBasedOnScroll

	useEffect(() => {
		carouselRef.current.style.pointerEvents = 'none';
		const int = 1500;
		window.setTimeout(() => {
			next();
		}, int);
		window.setTimeout(() => {
			next();
		}, int * 2);
		window.setTimeout(() => {
			next();
		}, int * 3);
		window.setTimeout(() => {
			next();
			carouselRef.current.style.pointerEvents = 'auto';
		}, int * 4);
	});

	//////////////////////////////////
	// set body style
	//
	// const style = document.createElement('style');
	// style.innerHTML = `body::-webkit-scrollbar {display: none;}`;
	// document.head.appendChild(style);

	document.body.style.overscrollBehaviorX = 'none';
	document.body.style.scrollBehaviorX = 'none';

	//
	//
	// End body style
	//////////////////////////////////////

	///////////////////////////////
	// Click and Drag:
	//

	const handleMouseMove = e => {
		if (!isDown) return;

		const drag = initClick - (e.clientX - determinedFrameSize + 14);

		carouselRef.current.scrollBy({
			left: drag,
			behavior: 'smooth',
		});
	};

	document.onmousemove = e => handleMouseMove(e);
	//
	// End Click and Drag
	///////////////////////////

	const next = () => {
		if (active === kids.length - 1) return;

		carouselRef.current.scrollBy({
			left: determinedFrameSize,
			behavior: 'smooth',
		});
	};

	const renderKids = () => {
		return kids.map((item, i) => {
			return (
				<Slide ref={slideRef} id={`Slide-${i}`} key={i}>
					{item}
				</Slide>
			);
		});
	};

	return (
		<>
			<Border id="Border">
				<CarouselDiv
					// onMouseDown={handleMouseDown}
					// onScroll={determineActive}
					ref={carouselRef}
					id="CarouselDiv"
				>
					{renderKids()}
				</CarouselDiv>
				{/* <Dots>{renderDots()}</Dots>
				<div className="arrows">
					{active === 0 ? (
						<span></span>
					) : (
						<div onClick={prev} className="arrow">
							{'<'}
						</div>
					)}
					{active === kids.length - 1 ? (
						<span></span>
					) : (
						<div onClick={next} className="arrow">
							{'>'}
						</div>
					)}
				</div> */}
			</Border>
		</>
	);
};

export default Carousel;
