import React from 'react';
import './styles.css';

export const useSortableData = (items, config = { key: 'fantasy' }) => {
    const [sortConfig, setSortConfig] = React.useState(config);

    const sortedItems = React.useMemo(() => {
        let sortableItems = [...items];
        if (sortConfig !== null) {
            sortableItems.sort((a, b) => {
                if (a[sortConfig.key] < b[sortConfig.key]) {
                    return sortConfig.direction === 'ascending' ? -1 : 1;
                }
                if (a[sortConfig.key] > b[sortConfig.key]) {
                    return sortConfig.direction === 'ascending' ? 1 : -1;
                }
                return 0;
            });
        }
        return sortableItems;
    }, [items, sortConfig]);

    const requestSort = key => {
        let direction = 'ascending';
        if (
            sortConfig &&
            sortConfig.key === key &&
            sortConfig.direction === 'ascending'
        ) {
            direction = 'descending';
        }
        setSortConfig({ key, direction });
    };

    return { items: sortedItems, requestSort, sortConfig };
};

export const SortableTable = props => {
    const { items, requestSort, sortConfig } = useSortableData(props.products);
    const getClassNamesFor = name => {
        if (!sortConfig) {
            return;
        }
        return sortConfig.key === name ? sortConfig.direction : undefined;
    };

    const [width, setWidth] = React.useState(window.innerWidth);
    function handleWindowSizeChange() {
        setWidth(window.innerWidth);
    }
    React.useEffect(() => {
        window.addEventListener('resize', handleWindowSizeChange);
        return () => {
            window.removeEventListener('resize', handleWindowSizeChange);
        };
    }, []);

    function determineAlive(name) {
        // console.log(name);

        const theDead = [
            'William Nylander',
            'Jack Campbell',
            'Kris Letang',
            'Evgeni Malkin',
            'Auston Matthews',
            'Morgan Rielly',
            'Jake Muzzin',
            'Sidney Crosby',
            'John Tavares',
            'Mark Scheifele',
            'Ryan Ellis',
            'T.J. Oshie',
            'Mitchell Marner',
            'Filip Forsberg',
            'Alex Pietrangelo',
            "Ryan O'Reilly",
            'Ryan Nugent-Hopkins',
            'Tom Wilson',
            'Tyson Barrie',
            'Ryan Suter',
            'Leon Draisaitl',
            'Alex Ovechkin',
            'Connor McDavid',
            'Roman Josi',
            'MacKenzie Weegar',
            'Jake Guentzel',
            'John Carlson',
            'Nick Foligno',
            'David Perron',
            'Petr Mrazek',
            'Aleksander Barkov',
            'Kirill Kaprizov',
            'Kevin Fiala',
            'Nicklas Backstrom',
            'Darnell Nurse',
        ];

        var isDead = false;
        theDead.forEach(item => {
            if (name === item) isDead = true;
        });
        if (isDead) return `❌ ${name} ❌`;
        else return name;
    }

    let isMobile = width <= 768;
    let mobDis = '';
    if (isMobile) mobDis = 'none';
    const stickyTop = '46px';

    const handleClick = item => {};

    return (
        <table style={isMobile ? { fontSize: '10px' } : {}}>
            <thead>
                <tr>
                    <th style={{ position: 'sticky', top: stickyTop }}>
                        <button
                            type="button"
                            onClick={() => requestSort('name')}
                            className={getClassNamesFor('name')}
                        >
                            Name
                        </button>
                    </th>
                    <th
                        style={{
                            position: 'sticky',
                            top: stickyTop,
                            display: mobDis,
                        }}
                    >
                        <button
                            type="button"
                            onClick={() => requestSort('number')}
                            className={getClassNamesFor('number')}
                        >
                            #
                        </button>
                    </th>
                    <th
                        style={{
                            position: 'sticky',
                            top: stickyTop,
                            display: mobDis,
                        }}
                    >
                        <button
                            type="button"
                            onClick={() => requestSort('pos')}
                            className={getClassNamesFor('pos')}
                        >
                            POS
                        </button>
                    </th>
                    <th style={{ position: 'sticky', top: stickyTop }}>
                        <button
                            type="button"
                            onClick={() => requestSort('goals')}
                            className={getClassNamesFor('goals')}
                        >
                            G
                        </button>
                    </th>
                    <th style={{ position: 'sticky', top: stickyTop }}>
                        <button
                            type="button"
                            onClick={() => requestSort('assists')}
                            className={getClassNamesFor('assists')}
                        >
                            A
                        </button>
                    </th>
                    <th
                        style={{
                            position: 'sticky',
                            top: stickyTop,
                            display: mobDis,
                        }}
                    >
                        <button
                            type="button"
                            onClick={() => requestSort('shg')}
                            className={getClassNamesFor('shg')}
                        >
                            SHG
                        </button>
                    </th>

                    <th
                        style={{
                            position: 'sticky',
                            top: stickyTop,
                            display: mobDis,
                        }}
                    >
                        <button
                            type="button"
                            onClick={() => requestSort('ppp')}
                            className={getClassNamesFor('ppp')}
                        >
                            PPP
                        </button>
                    </th>
                    <th
                        style={{
                            position: 'sticky',
                            top: stickyTop,
                            display: mobDis,
                        }}
                    >
                        <button
                            type="button"
                            onClick={() => requestSort('wins')}
                            className={getClassNamesFor('wins')}
                        >
                            Wins
                        </button>
                    </th>
                    <th
                        style={{
                            position: 'sticky',
                            top: stickyTop,
                            display: mobDis,
                        }}
                    >
                        <button
                            type="button"
                            onClick={() => requestSort('ga')}
                            className={getClassNamesFor('ga')}
                        >
                            GA
                        </button>
                    </th>
                    <th
                        style={{
                            position: 'sticky',
                            top: stickyTop,
                            display: mobDis,
                        }}
                    >
                        <button
                            type="button"
                            onClick={() => requestSort('shutouts')}
                            className={getClassNamesFor('shutouts')}
                        >
                            SO
                        </button>
                    </th>
                    <th
                        style={{
                            position: 'sticky',
                            top: stickyTop,
                            display: mobDis,
                        }}
                    >
                        <button
                            type="button"
                            onClick={() => requestSort('gp')}
                            className={getClassNamesFor('gp')}
                        >
                            GP
                        </button>
                    </th>
                    <th style={{ position: 'sticky', top: stickyTop }}>
                        <button
                            type="button"
                            onClick={() => requestSort('fantasy')}
                            className={getClassNamesFor('fantasy')}
                        >
                            FP
                        </button>
                    </th>
                    <th style={{ position: 'sticky', top: stickyTop }}>
                        <button
                            type="button"
                            onClick={() => requestSort('avg')}
                            className={getClassNamesFor('avg')}
                        >
                            AVG
                        </button>
                    </th>
                </tr>
            </thead>
            <tbody>
                {items.map((item, i) => (
                    <tr
                        onClick={() => handleClick(item)}
                        key={`${item.id}-${i}`}
                    >
                        <td
                            style={
                                sortConfig && sortConfig.key === 'name'
                                    ? { background: 'white' }
                                    : {}
                            }
                        >
                            {determineAlive(item.name)}
                        </td>
                        <td
                            style={
                                sortConfig && sortConfig.key === 'number'
                                    ? { display: mobDis, background: 'white' }
                                    : { display: mobDis }
                            }
                        >
                            {item.number}
                        </td>
                        <td
                            style={
                                sortConfig && sortConfig.key === 'pos'
                                    ? { display: mobDis, background: 'white' }
                                    : { display: mobDis }
                            }
                        >
                            {item.pos}
                        </td>
                        <td
                            style={
                                sortConfig && sortConfig.key === 'goals'
                                    ? { background: 'white' }
                                    : {}
                            }
                        >
                            {item.goals || 0}
                        </td>
                        <td
                            style={
                                sortConfig && sortConfig.key === 'assists'
                                    ? { background: 'white' }
                                    : {}
                            }
                        >
                            {item.assists || 0}
                        </td>

                        <td
                            style={
                                sortConfig && sortConfig.key === 'shg'
                                    ? { display: mobDis, background: 'white' }
                                    : { display: mobDis }
                            }
                        >
                            {item.shortHandedGoals}
                        </td>

                        <td
                            style={
                                sortConfig && sortConfig.key === 'ppp'
                                    ? { display: mobDis, background: 'white' }
                                    : { display: mobDis }
                            }
                        >
                            {item.powerPlayPoints}
                        </td>
                        <td
                            style={
                                sortConfig && sortConfig.key === 'wins'
                                    ? { display: mobDis, background: 'white' }
                                    : { display: mobDis }
                            }
                        >
                            {item.wins}
                        </td>
                        <td
                            style={
                                sortConfig && sortConfig.key === 'ga'
                                    ? { display: mobDis, background: 'white' }
                                    : { display: mobDis }
                            }
                        >
                            {item.ga}
                        </td>
                        <td
                            style={
                                sortConfig && sortConfig.key === 'shutouts'
                                    ? { display: mobDis, background: 'white' }
                                    : { display: mobDis }
                            }
                        >
                            {item.shutouts}
                        </td>
                        <td
                            style={
                                sortConfig && sortConfig.key === 'gp'
                                    ? { display: mobDis, background: 'white' }
                                    : { display: mobDis }
                            }
                        >
                            {item.gp}
                        </td>
                        <td
                            style={
                                sortConfig && sortConfig.key === 'fantasy'
                                    ? { background: 'white' }
                                    : {}
                            }
                        >
                            {item.fantasy || 0}
                        </td>
                        <td
                            style={
                                sortConfig && sortConfig.key === 'avg'
                                    ? { background: 'white' }
                                    : {}
                            }
                        >
                            {isNaN(item.avg) ? 0 : item.avg}
                        </td>
                    </tr>
                ))}
            </tbody>
        </table>
    );
};

