import React from 'react';

import { SortableTable } from './SortableTable';

import { finalTeamPlayers } from './atoms';
import { useRecoilState } from 'recoil';

export default function ShowTeam(props) {
    const { team, teamCode } = props;

    const [LfinalTeamPlayers] = useRecoilState(finalTeamPlayers);

    return (
        <SortableTable
            team={team}
            type="display"
            products={LfinalTeamPlayers[teamCode]}
        />
    );
}
