                                        // at end

                                        // console.log(playOffTeams.CBD.team);

                                        function calcFant(i) {
                                            if (!i) return;
                                            if (i.pos === 'G') {
                                                // console.log('from index', {
                                                //     i,
                                                // });
                                                const shutouts = i.shutouts * 2;
                                                const goalsAgainst =
                                                    i.ga * -0.25;
                                                const gamesStarted =
                                                    i.gp * 2 || 0;
                                                const wins = i.wins * 2;
                                                // const name = i.name;
                                                // console.log({
                                                //     name,
                                                //     shutouts,
                                                //     goalsAgainst,
                                                //     gamesStarted,
                                                //     wins,
                                                // });
                                                // console.log({
                                                //     shutouts,
                                                //     goalsAgainst,
                                                //     gamesStarted,
                                                //     wins,
                                                // });
                                                return (
                                                    shutouts +
                                                    goalsAgainst +
                                                    gamesStarted +
                                                    wins
                                                );
                                            } else {
                                                const goals = i.goals * 3 || 0;
                                                const assists =
                                                    i.assists * 2 || 0;
                                                const powerPlayPoints =
                                                    i.powerPlayPoints || 0;
                                                const shortHandedGoals =
                                                    i.shortHandedGoals * 2 || 0;
                                                // console.log({
                                                //     goals,
                                                //     assists,
                                                //     powerPlayPoints,
                                                //     shortHandedGoals,
                                                // });
                                                // console.log(goals +
                                                //     assists +
                                                //     powerPlayPoints +
                                                //     shortHandedGoals);
                                                return (
                                                    goals +
                                                    assists +
                                                    powerPlayPoints +
                                                    shortHandedGoals
                                                );
                                            }
                                        }

                                        let score = 0;
                                        finalData.CBD.forEach(item => {
                                            score += calcFant(item);
                                        });
                                        // console.log('CBD score', score);
                                        newData.CBD.FP = score;

                                        score = 0;
                                        finalData.PEN.forEach(item => {
                                            score += calcFant(item);
                                        });
                                        // console.log('PEN score', score);
                                        newData.PEN.FP = score;

                                        score = 0;
                                        finalData.GLA.forEach(item => {
                                            score += calcFant(item);
                                        });
                                        // console.log('GLA score', score);
                                        newData.GLA.FP = score;

                                        // if (finalData.SUT[0]===null) finalData.SUT.pop()
                                        score = 0;
                                        finalData.SUT.forEach(item => {
                                            if (item) score += calcFant(item);
                                        });
                                        // console.log('SUT score', score);
                                        newData.SUT.FP = score;

                                        // console.log({ newData });

                                        setCalcTeams(newData);
                                        // console.log({ finalData });

                                        // console.log(finalData);
                                        setFinalTeamPlayers(finalData);

                                        if (isMobile) {
                                            setCurrentPage('box');
                                        }

                                        const rand =
                                            finalSayings[
                                                Math.floor(
                                                    Math.random() *
                                                        finalSayings.length
                                                )
                                            ];
