/* eslint-disable react-hooks/exhaustive-deps */
import React, { useState, useEffect } from 'react';

import Header from './Header';

import Lock from './Lock';

import BoxScore from './BoxScore';
import Loading from './Loading';
import ShowTeam from './ShowTeam';

import styled from 'styled-components';

import { db } from '../ContactForm/firebase';

import AdminTable from './AdminTable';

import { useRecoilState } from 'recoil';
import { currentPage, user, euroTeams } from './atoms';
import { cloneDeep } from 'lodash';

export default function Euros() {
    const [localCurrentPage] = useRecoilState(currentPage);
    const [euroTeamsLocal, setEuroTeams] = useRecoilState(euroTeams);
    const [localUser] = useRecoilState(user);

    document.title = 'Euros 2021';

    const [isLoaded, setIsLoaded] = useState(false);

    const [loadInit, setLoadInit] = useState(false);

    useEffect(() => {
        if (loadInit) return;
        setLoadInit(true);
        // console.log('🎆 useEffect run');

        let myData = [];

        db.collection('Euros')
            .get()
            .then(querySnapshot => {
                const data = querySnapshot.docs.map(doc => doc.data());
                // console.log(data.length === 0);
                if (data.length > 0) {
                    myData = cloneDeep(data[0].data);
                    myData.forEach((item, i) => {
                        let score = 0;
                        item.tableData.forEach(player => {
                            const { fantasy } = player;
                            score += fantasy;
                        });
                        myData[i].score = score;
                    });
                } else {
                    myData = euroTeamsLocal;
                }
            })
            .then(() => {
                myData.sort((a, b) => {
                    return a.score - b.score;
                });
                myData.reverse();
            })
            .then(() => {
                window.setTimeout(() => {
                    setEuroTeams(myData);
                    setIsLoaded(true);
                }, 2000);
            });
    }, []);

    if (!localUser) return <Lock />;
    if (localUser && !isLoaded)
        return (
            <Wrap style={{ height: '100vh' }}>
                <Loading loadingPhase={null} />
            </Wrap>
        );
    return (
        <>
            <meta
                name="viewport"
                content="width=device-width, initial-scale=1.0, maximum-scale=1.0, user-scalable=0"
            />
            <Header />
            <Wrap id="Wrap">
                {localUser === 'ADMIN' ? <h1>Admin Powers Active!</h1> : null}
                {localCurrentPage === 'box' ? (
                    <BoxScore />
                ) : localCurrentPage === 'ADMIN' ? (
                    <AdminTable />
                ) : (
                    <ShowTeam team={localCurrentPage} />
                )}
            </Wrap>
        </>
    );
}

