import React, { useRef, useState, useEffect } from 'react';

import { useRecoilState } from 'recoil';
import { currentAward } from './atoms';

import styled from 'styled-components';

const CarouselDiv = styled.div`
	display: grid;
	grid-template-columns: repeat(50, 100vw);
	height: 100vh;
	overflow-y: scroll;
	scrollbar-width: none;
	scroll-behavior: smooth;

	scroll-snap-type: x mandatory;

	cursor: grab;

	.grabbing {
		cursor: grabbing;
	}

	&::-webkit-scrollbar {
		display: none;
	}

	-ms-overflow-style: none;
	scrollbar-width: none;
	-webkit-overflow-scrolling: touch;
`;

const Slide = styled.div`
	width: 100vw;
	display: flex;
	justify-content: center;
	scroll-snap-align: start;
	position: relative;
	top: 80px;
`;

const Border = styled.div`
	height: 100vh;
	width: 100vw;
	overflow: hidden;
	position: absolute;

	.arrows {
		position: relative;
		display: flex;
		top: -50vh;
		justify-content: space-between;
		-webkit-touch-callout: none;
		-webkit-user-select: none;
		-khtml-user-select: none;
		-moz-user-select: none;
		-ms-user-select: none;
		user-select: none;
	}

	.arrow {
		z-index: 100000000000;
		opacity: 0.7;
		background: grey;
		display: flex;
		align-items: center;
		justify-content: center;
		position: relative;
		padding: 0.5rem;
		cursor: pointer;
		transition: all 400ms;

		&:hover {
			opacity: 1;
			transition: all 400ms;
		}
	}

	.prev {
		top: -14rem;
		left: -9rem;
	}
`;

const Carousel = props => {
	const [localCurrentAward, setCurrentAward] = useRecoilState(currentAward);

	// Props
	const kids = props.children;

	// Refs
	const carouselRef = useRef(localCurrentAward);
	const slideRef = useRef(null);

	// State
	const [determinedFrameSize, setDeterminedFrameSize] = useState(null);
	const [initClick, setInitClick] = useState(null);
	const [isDown, setIsDown] = useState(false);

	// Determine Slide Size
	useEffect(() => {
		if (!slideRef) return;
		setDeterminedFrameSize(slideRef.current.clientWidth);
	}, [determinedFrameSize]);

	// setActiveBasedOnScroll
	const determineActive = () => {
		if (!carouselRef) return;

		function isWhole(n) {
			var result = n - Math.floor(n) !== 0;

			if (result) return false;
			else return true;
		}

		const currentSlide = carouselRef.current.scrollLeft / determinedFrameSize;

		if (isWhole(currentSlide) && currentSlide !== localCurrentAward) {
			setCurrentAward(
				Math.floor(carouselRef.current.scrollLeft / determinedFrameSize)
			);
			carouselRef.current.scrollTo({
				top: 0,
			});
		}
		// setActive(Math.floor(carouselRef.current.scrollLeft / determinedFrameSize));
	};

	useEffect(() => {
		// setActive(2);
		carouselRef.current.scrollTo({
			left: localCurrentAward * determinedFrameSize,
			top: 0,
		});
	}, [localCurrentAward, determinedFrameSize]);

	//////////////////////////////////
	// set body style
	//
	const style = document.createElement('style');
	style.innerHTML = `body::-webkit-scrollbar {display: none;}`;
	document.head.appendChild(style);

	document.body.style.overscrollBehaviorX = 'none';

	//
	//
	// End body style
	//////////////////////////////////////

	///////////////////////////////
	// Click and Drag:
	//
	const handleMouseDown = e => {
		e.preventDefault();

		carouselRef.current.style.cursor = 'grabbing';

		setIsDown(true);

		setInitClick(e.clientX - determinedFrameSize + 14);
	};

	document.onmouseup = e => {
		if (!isDown) return;
		determineActive();
		setIsDown(false);
		carouselRef.current.style.cursor = 'grab';
	};

	const handleMouseMove = e => {
		if (!isDown) return;

		const drag = initClick - (e.clientX - determinedFrameSize + 14);

		carouselRef.current.scrollBy({
			left: drag,
			behavior: 'smooth',
		});
	};

	document.onmousemove = e => handleMouseMove(e);
	//
	// End Click and Drag
	///////////////////////////

	const prev = () => {
		if (localCurrentAward === 0) return;

		setCurrentAward(localCurrentAward - 1);
	};

	const next = () => {
		if (localCurrentAward === kids.length - 1) return;

		// carouselRef.current.scrollBy({
		// 	left: determinedFrameSize,
		// 	behavior: 'smooth',
		// });
		// setActive(active + 1);
		setCurrentAward(localCurrentAward + 1);
	};

	const renderKids = () => {
		return kids.map((item, i) => {
			return (
				<Slide ref={slideRef} id={`Slide-${i}`} key={i}>
					{item}
				</Slide>
			);
		});
	};

	const scrollTopRef = useRef(null);
	return (
		<>
			<Border id="Border" ref={scrollTopRef}>
				<CarouselDiv
					onMouseDown={handleMouseDown}
					onScroll={determineActive}
					ref={carouselRef}
					id="CarouselDiv"
				>
					{renderKids()}
				</CarouselDiv>
				<div className="arrows">
					{localCurrentAward === 0 ? (
						<span></span>
					) : (
						<div onClick={prev} className="arrow">
							{'<'}
						</div>
					)}
					{localCurrentAward === kids.length - 1 ? (
						<span></span>
					) : (
						<div onClick={next} className="arrow">
							{'>'}
						</div>
					)}
				</div>
			</Border>
		</>
	);
};

export default Carousel;
