import React, { useEffect, useRef } from 'react';

import { gsap } from 'gsap';

import { HeadCon } from './HeadStyle';

import { Link } from 'react-router-dom';

function Header() {
	const headRef = useRef();

	const sections = ['About', 'Projects', 'Contact'];

	useEffect(() => {
		gsap.from(headRef.current, {
			duration: 0.7,
			y: -100,
			ease: 'power1',
		});
		gsap.from(headRef.current, {
			duration: 1.4,
			opacity: 0.1,
			ease: 'power1',
		});
	}, []);

	function renderSections() {
		return sections.map((section, i) => {
			let sect = section.toString();
			return (
				<p
					className="head-link"
					key={section}
					onClick={() => scrollToSection(sect)}
				>
					{section}
				</p>
			);
		});
	}

	function scrollToSection(s) {
		let elm = document.getElementById(s);
		if (elm && elm === 'About') {
		} else {
			elm.scrollIntoView({
				behavior: 'smooth',
				block: 'start',
				inline: 'nearest',
			});
		}
		window.setTimeout(() => {
			window.scrollBy(1, 1);
		}, 1000);
	}

	return (
		<HeadCon ref={headRef} id="HeadCon">
			<Link style={{ textDecoration: 'none' }} to="contact" target="_blank">
				<h1>Rick Gove</h1>
			</Link>
			{renderSections()}
		</HeadCon>
	);
}

export default Header;
