import React, { useState } from 'react';

import { useDispatch } from 'react-redux';

import { useAlert } from 'react-alert';

import axios from 'axios';

import {
	setRecipeResults,
	setInputFocus,
	setRecipeDetails,
} from '../../../../actions';

export default function SearchBar() {
	const [value, setValue] = useState('');
	const [prevSearch, setprevSearch] = useState('');
	const key = `ca73346a72cb4ae09a9119aafe5026ee`;
	const dispatch = useDispatch();

	const alert = useAlert();

	async function getResults() {
		if (value === prevSearch) {
			setprevSearch(value);
			const sameSearch = alert.error('Enter new search', {
				timeout: 1000,
			});
			window.setTimeout(() => {
				alert.remove(sameSearch);
			}, 5000);
		} else {
			setprevSearch(value);
			const searchAlert = alert.show('Searching . . .', {
				timeout: 100000,
			});
			dispatch(setRecipeResults([]));

			try {
				const res = await axios(
					`https://api.spoonacular.com/recipes/search?query=${value}&number=500&apiKey=${key}`
				);
				const result = res.data.results;
				dispatch(setRecipeResults(result));
				dispatch(setRecipeDetails([]));
				if (result.length === 0) {
					alert.remove(searchAlert);
					alert.error('No results ...');
				} else {
					alert.remove(searchAlert);
					alert.success(`${result.length} results!`);
				}
			} catch (error) {
				alert.error('Something went wrong, try again');
			}
		}
		const formEl = document.getElementById('search-form');
		formEl.blur();
	}

	function setFocus() {
		dispatch(setInputFocus(true));
	}

	function setBlur() {
		dispatch(setInputFocus(false));
	}

	return (
		<div className="search-container">
			<form
				onFocus={setFocus}
				onBlur={setBlur}
				onSubmit={getResults}
				className="search"
			>
				<span role="img" aria-label="search">
					🔎
				</span>
				<input
					id="search-form"
					placeholder={'1,000,000+ Recipes'}
					value={value}
					onChange={e => {
						setValue(e.target.value);
					}}
					type="text"
					autoComplete="off"
					className="search__field"
				/>
				<button className="btn search__btn">Search</button>
			</form>
		</div>
	);
}
