import React, { useState, useEffect } from 'react';

import { Menu } from 'antd';
import 'antd/dist/antd.css';

import { useRecoilState } from 'recoil';
import { currentPage, user } from './atoms';
import IndTeamHeader from './IndTeamHeader';

export default function Header() {
    const [localCurrentPage, setCurrentPage] = useRecoilState(currentPage);
    const [lUser] = useRecoilState(user);

    const [width, setWidth] = useState(window.innerWidth);
    function handleWindowSizeChange() {
        setWidth(window.innerWidth);
    }
    useEffect(() => {
        window.addEventListener('resize', handleWindowSizeChange);
        return () => {
            window.removeEventListener('resize', handleWindowSizeChange);
        };
    }, []);

    let isMobile = width <= 768;

    function scrollToTop() {
        window.scrollTo(0, 0);
    }

    // console.log({ localCurrentPage });

    return (
        <>
            <Menu
                style={{
                    zIndex: 99999999999999,
                    position: 'sticky',
                    top: 0,
                    boxShadow: '0 3px 5px rgba(57, 63, 72, 0.3)',
                }}
                onClick={e => {
                    setCurrentPage(e.key);
                    scrollToTop();
                }}
                selectedKeys={localCurrentPage}
                mode="horizontal"
            >
                <Menu.Item key="my-team">
                    <span role="img" aria-label="news">
                        {lUser === 'CBD'
                            ? '🇨🇦'
                            : lUser === 'GLA'
                            ? '🏴󠁧󠁢󠁳󠁣󠁴󠁿'
                            : lUser === 'PEN'
                            ? '🐼'
                            : '🎉'}
                    </span>
                    {!isMobile ? ' My Team' : null}
                    {'    '}
                </Menu.Item>

                {lUser !== 'CBD' ? (
                    <Menu.Item key="CBD">
                        <span role="img" aria-label="news">
                            🇨🇦
                        </span>
                        {!isMobile ? ` CBD` : null}
                    </Menu.Item>
                ) : null}
                {lUser !== 'GLA' ? (
                    <Menu.Item key="GLA">
                        <span role="img" aria-label="news">
                            🏴󠁧󠁢󠁳󠁣󠁴󠁿
                        </span>
                        {!isMobile ? ' GLA' : null}
                    </Menu.Item>
                ) : null}
                {lUser !== 'SUT' ? (
                    <Menu.Item key="SUT">
                        <span role="img" aria-label="news">
                            🎉
                        </span>
                        {!isMobile ? ' SUT' : null}
                    </Menu.Item>
                ) : null}
                {lUser !== 'PEN' ? (
                    <Menu.Item key="PEN">
                        <span role="img" aria-label="news">
                            🐼
                        </span>
                        {!isMobile ? ' PEN' : null}
                    </Menu.Item>
                ) : null}
                <Menu.Item key="box">
                    <span role="img" aria-label="news">
                        🧮
                    </span>

                    {!isMobile ? ' Scores' : null}
                </Menu.Item>
                <Menu.Item key="picks">
                    <span role="img" aria-label="news">
                        ✅
                    </span>
                    {!isMobile ? ' Picks' : null}
                </Menu.Item>
                {/* Draft Players Removed */}
                {/* {!isMobile ? (
                    <Menu.Item key="add">
                        <span role="img" aria-label="news">
                            ➕
                        </span>
                        {'      '}
                        Draft Players
                    </Menu.Item>
                ) : null} */}

                {localCurrentPage === 'SUT' ||
                localCurrentPage === 'CBD' ||
                localCurrentPage === 'GLA' ||
                localCurrentPage === 'PEN' ||
                localCurrentPage === 'my-team' ? (
                    <IndTeamHeader isMobile={isMobile} />
                ) : null}
            </Menu>
        </>
    );
}
