import React, { useEffect, useRef, useState } from 'react';

import { gsap } from 'gsap';

import { ShowCaseCon } from './AboutStyle';

import { Link } from 'react-router-dom';

import meA from '../../img/RBG.jpg';

import Button from '../Button';

import { Linkedin } from '@styled-icons/fa-brands/Linkedin';

import { Github } from '@styled-icons/evaicons-solid/Github';

export default function About() {
	console.clear();
	console.log(
		'%cThank You!',
		'font-size: 2rem; background: black; color: dodgerblue'
	);
	console.log(
		'%cFor checking out my portfolio.',
		'font-size: 1.4rem; background: black; color: dodgerblue'
	);

	console.log(
		'%cI hope you enjoy it and decide to contact me.',
		'font-size: 1.4rem; background: black; color: dodgerblue'
	);
	const [width, setWidth] = useState(window.innerWidth);
	function handleWindowSizeChange() {
		setWidth(window.innerWidth);
	}
	useEffect(() => {
		window.addEventListener('resize', handleWindowSizeChange);
		return () => {
			window.removeEventListener('resize', handleWindowSizeChange);
		};
	}, []);
	let isMobile = width <= 740;
	const anB = useRef();

	useEffect(() => {
		const delay = 0.9;

		gsap.from(anB.current, {
			duration: delay,
			y: 90,
			ease: 'power3',
		});

		gsap.from(anB.current, {
			duration: 1.4,
			opacity: 0.1,
			ease: 'power1',
		});
	}, []);

	function tagLine() {
		return <h2>A web designer from Canada. My passion is making websites.</h2>;
	}

	if (!isMobile) {
		return (
			<ShowCaseCon id="About">
				<div className="spacer-small" />
				<div ref={anB} className="about-container">
					<div className="about-text">
						<h1 style={{ color: '#a9a9a9' }}>Hey, I'm Rick.</h1>
						{tagLine()}
						<Link target="_blank" to="contact">
							<Button label="contact me" />
						</Link>
					</div>
					<div className="about-img-div">
						<figure className="about-img">
							<img src={meA} alt="Rick"></img>
						</figure>
						<span className="contact-link">
							<a
								rel="noopener noreferrer"
								target="_blank"
								href="https://www.linkedin.com/in/rick-gove"
							>
								<Linkedin className="icon" size="21" title="email" />
								{'            '}LinkedIn{'            '}
							</a>
						</span>
						<span className="contact-link">
							<a
								rel="noopener noreferrer"
								target="_blank"
								href="https://www.github.com/rickgove"
							>
								<Github className="icon" size="21" title="email" />
								{'            '}GitHub
							</a>
						</span>
					</div>
				</div>
				<div className="spacer" />
			</ShowCaseCon>
		);
	} else {
		return (
			<ShowCaseCon id="About">
				<div className="spacer-small" />
				<div ref={anB} className="about-container">
					<div className="about-text mobile">
						<h1>Hey, I'm Rick.</h1>
						<figure className="about-img">
							<img src={meA} alt="Rick"></img>
						</figure>
						{tagLine()}
					</div>
					<div className="about-img-div short">
						<Link target="_blank" to="contact">
							<Button label="contact me" />
						</Link>
						<span className="contact-link">
							<a
								rel="noopener noreferrer"
								target="_blank"
								href="https://www.linkedin.com/in/rick-gove"
							>
								<Linkedin className="icon" size="21" title="email" />
								{'            '}LinkedIn{'            '}
							</a>
						</span>
						<span className="contact-link">
							<a
								rel="noopener noreferrer"
								target="_blank"
								href="https://www.github.com/rickgove"
							>
								<Github className="icon" size="21" title="email" />
								{'            '}GitHub
							</a>
						</span>
					</div>
				</div>
				<div className="spacer" />
			</ShowCaseCon>
		);
	}
}
