import React, { useEffect, useRef } from 'react';

import gsap from 'gsap';

import { SideTitleDiv } from './style';

import { useDispatch } from 'react-redux';

import { setRecipeDetails } from '../../../../actions';

export default function SideTitle(args) {
	const dispatch = useDispatch();

	const main = useRef();

	useEffect(() => {
		gsap.from(main.current, { opacity: 0, duration: 4 });
	});

	function clearRecipe() {
		dispatch(setRecipeDetails([]));
	}

	return (
		// <div ref={main}>
		<SideTitleDiv>
			<button onClick={clearRecipe}>
				<span ref={main} aria-label="back" role="img">
					⬅️
				</span>
			</button>
			<h1>{args.title}</h1>
		</SideTitleDiv>
		// </div>
	);
}

SideTitle.defaultProps = {
	title: 'enter recipe name',
};
