import React from 'react';
import './styles.css';

import { db } from '../../ContactForm/firebase';

import { cloneDeep } from 'lodash';
import { euroTeams, currentPage, user } from '../atoms';
import { useRecoilState } from 'recoil';
import { useAlert, positions } from 'react-alert';

export const useSortableData = (items, config = { key: 'fantasy' }) => {
    const [sortConfig, setSortConfig] = React.useState(config);

    // console.log({ items });

    const sortedItems = React.useMemo(() => {
        let sortableItems = [...items];
        if (sortConfig !== null) {
            sortableItems.sort((a, b) => {
                // console.log({ a, b });
                // if (!a) console.log({ a, b });
                // if (!b) console.log({ a, b });
                // console.log(
                //     'sorting a, b',
                //     a[sortConfig.key],
                //     b[sortConfig.key]
                // );
                // console.log(sortConfig.key);
                if (a[sortConfig.key] < b[sortConfig.key]) {
                    return sortConfig.direction === 'ascending' ? -1 : 1;
                }
                if (a[sortConfig.key] > b[sortConfig.key]) {
                    return sortConfig.direction === 'ascending' ? 1 : -1;
                }
                return 0;
            });
        }
        return sortableItems;
    }, [items, sortConfig]);

    const requestSort = key => {
        let direction = 'ascending';
        if (
            sortConfig &&
            sortConfig.key === key &&
            sortConfig.direction === 'ascending'
        ) {
            direction = 'descending';
        }
        setSortConfig({ key, direction });
    };

    return { items: sortedItems, requestSort, sortConfig };
};

export const SortableTable = props => {
    const { items = [], requestSort, sortConfig } = useSortableData(
        props.products
    );
    const getClassNamesFor = name => {
        if (!sortConfig) {
            return;
        }
        return sortConfig.key === name ? sortConfig.direction : undefined;
    };

    const [width, setWidth] = React.useState(window.innerWidth);
    function handleWindowSizeChange() {
        setWidth(window.innerWidth);
    }
    React.useEffect(() => {
        window.addEventListener('resize', handleWindowSizeChange);
        return () => {
            window.removeEventListener('resize', handleWindowSizeChange);
        };
    }, []);

    let isMobile = width <= 768;
    let mobDis = '';
    if (isMobile) mobDis = 'none';
    const stickyTop = '46px';

    const Ralert = useAlert();
    function updatePlayer(name, pos, stat) {
        let newTeams = cloneDeep(teams);
        newTeams.forEach((item, i) => {
            // console.log(item);
            item.tableData.forEach((player, pi) => {
                // console.log(player);
                if (player.name === name) {
                    newTeams[i]['tableData'][pi] = {
                        name,
                        pos,
                        ...stat,
                    };
                }
            });
        });
        db.collection('Euros')
            .doc('Teams')
            .set({ data: newTeams })
            .then(() => {
                Ralert.success(`${name} saved`, {
                    timeout: 2000,
                    position: positions.BOTTOM_CENTER,
                });
            })
            .catch(err => {
                alert('error ', err.message);
            });
        // console.log(newTeams);
        setTeams(newTeams);
    }

    function calcFant(stat, pos) {
        // console.log(stat);
        let goalVal;
        if (pos === 'F') goalVal = 4 * stat.goals;
        if (pos === 'M') goalVal = 6 * stat.goals;
        if (pos === 'M*') goalVal = 7 * stat.goals;
        if (pos === 'D') goalVal = 8 * stat.goals;
        if (pos === 'GK') goalVal = 15 * stat.goals;

        let csVal;
        if (pos === 'F') csVal = 5 * stat.cs;
        if (pos === 'M') csVal = 1 * stat.cs;
        if (pos === 'M*') csVal = 3 * stat.cs;
        if (pos === 'D') csVal = 5 * stat.cs;
        if (pos === 'GK') csVal = 5 * stat.cs;

        const aVal = stat.assists * 3;

        const gcVal = -stat.gc;

        const pcVal = -stat.pc;

        const pmVal = -stat.pm * 2;

        const psVal = stat.ps * 2;

        const ogVal = -stat.og * 2;

        const obVal = -stat.ob * 2;

        const rcVal = -stat.rc * 2;
        const fant =
            goalVal +
            csVal +
            stat.gp +
            stat.sub +
            aVal +
            gcVal +
            pcVal +
            pmVal +
            psVal +
            ogVal +
            obVal +
            rcVal;
        return fant;
    }

    async function handleClick(item) {
        if (localUser !== 'ADMIN') return;
        alert(`Editing ${item.name}`);
        // console.log(item);
        let gp = parseInt(prompt(`Set Game Played? (${item.gp})`));
        if (isNaN(gp)) gp = item.gp;
        let sub = parseInt(prompt(`Set Sub? (${item.sub})`));
        if (isNaN(sub)) sub = item.sub;
        let goals = parseInt(prompt(`Set Goals? (${item.goals})`));
        if (isNaN(goals)) goals = item.goals;
        let assists = parseInt(prompt(`Set Assists? (${item.assists})`));
        if (isNaN(assists)) assists = item.assists;
        let cs = parseInt(prompt(`Set Clean Sheet? (${item.cs})`));
        if (isNaN(cs)) cs = item.cs;
        let gc = parseInt(prompt(`Set Goals Conceded? (${item.gc})`));
        if (isNaN(gc)) gc = item.gc;
        let pc = parseInt(prompt(`Set Penalty Conceded? (${item.pc})`));
        if (isNaN(pc)) pc = item.pc;
        let pm = parseInt(prompt(`Set Penalty Missed? (${item.pm})`));
        if (isNaN(pm)) pm = item.pm;
        let ps = parseInt(prompt(`Set Penalty Scored? (${item.ps})`));
        if (isNaN(ps)) ps = item.ps;
        let og = parseInt(prompt(`Set Own Goal? (${item.og})`));
        if (isNaN(og)) og = item.og;
        let rc = parseInt(prompt(`Set Red Card? (${item.rc})`));
        if (isNaN(rc)) rc = item.rc;
        let ob = parseInt(prompt(`Set Obv Blunder? (${item.ob})`));
        if (isNaN(ob)) ob = item.ob;
        let stat = {
            sub,
            goals,
            assists,
            cs,
            gc,
            pc,
            pm,
            ps,
            gp,
            og,
            rc,
            ob,
        };
        const fantasy = calcFant(stat, item.pos);
        stat.fantasy = fantasy;
        // console.log({ fantasy });
        const totalGP = stat.gp + stat.sub;
        const avg = fantasy / totalGP;
        // console.log(avg);
        stat.avg = avg.toFixed(1);

        updatePlayer(item.name, item.pos, stat);
    }

    const [teams, setTeams] = useRecoilState(euroTeams);
    const [curPage] = useRecoilState(currentPage);

    const [localUser] = useRecoilState(user);

    const addPlayer = () => {
        // {name: 'Bob', sub: 0, pos: 'G', goals: 20, assists: 10, cs: 5, gc: 10, pc: 40, pm: 11, ps: 99, gp: 100, rc: 100, ob: 10}
        let newTeams = cloneDeep(teams);
        let updatedTeams = [];
        // console.log('run');
        // console.log({ newTeams, curPage });
        let name = prompt('Name?');
        if (!name) {
            Ralert.error(`!Name forbidden`, {
                timeout: 2000,
                position: positions.BOTTOM_CENTER,
            });
            return;
        }
        let pos = prompt('Pos?');
        if (
            pos !== 'GK' &&
            pos !== 'F' &&
            pos !== 'M' &&
            pos !== 'M*' &&
            pos !== 'F'
        ) {
            Ralert.error(`Invalid Pos`, {
                timeout: 2000,
                position: positions.BOTTOM_CENTER,
            });
            return;
        }
        newTeams.forEach(item => {
            // console.log(item.shortName === curPage);
            if (item.shortName === curPage) {
                item.tableData.push({
                    name: name,
                    sub: 0,
                    pos: pos,
                    goals: 0,
                    assists: 0,
                    cs: 0,
                    gc: 0,
                    pc: 0,
                    pm: 0,
                    ps: 0,
                    gp: 0,
                    rc: 0,
                    og: 0,
                    ob: 0,
                    avg: 0,
                    fantasy: 0,
                });
            }
            updatedTeams.push(item);
        });

        // console.log(updatedTeams);
        db.collection('Euros')
            .doc('Teams')
            .set({ data: updatedTeams })
            .then(() => {
                Ralert.success(`${name} saved`, {
                    timeout: 2000,
                    position: positions.BOTTOM_CENTER,
                });
            })
            .then(() => {
                db.collection('Euros')
                    .get()
                    .then(querySnapshot => {
                        const data = querySnapshot.docs.map(doc => doc.data());
                        // console.log(data.length === 0);
                        if (data.length > 0) {
                            const myData = cloneDeep(data[0].data);
                            // console.log(myData);
                            let score = 0;
                            // console.log('1', { score });
                            myData.forEach((item, i) => {
                                // console.log(`2`, { score });

                                item.tableData.forEach((player, ind) => {
                                    myData[i].score = score + player.fantasy;
                                    // console.log('3', { score });
                                });
                            });
                            setTeams(myData);
                        }
                    });
            })
            .catch(err => {
                alert('error ', err.message);
            });

        setTeams(updatedTeams);
    };

    const sortBG = 'white';

    return (
        <>
            {localUser === 'ADMIN' && !props.isAdmin ? (
                <button className="add-player-btn" onClick={addPlayer}>
                    Add Player
                </button>
            ) : null}
            <table style={isMobile ? { fontSize: '10px' } : {}}>
                <thead
                    style={localUser === 'ADMIN' ? { cursor: 'pointer' } : {}}
                >
                    <tr>
                        <th style={{ position: 'sticky', top: stickyTop }}>
                            <button
                                type="button"
                                onClick={() => requestSort('name')}
                                className={getClassNamesFor('name')}
                            >
                                Name
                            </button>
                        </th>

                        <th
                            style={{
                                position: 'sticky',
                                top: stickyTop,
                                display: mobDis,
                            }}
                        >
                            <button
                                type="button"
                                onClick={() => requestSort('pos')}
                                className={getClassNamesFor('pos')}
                            >
                                POS
                            </button>
                        </th>
                        <th
                            style={{
                                position: 'sticky',
                                top: stickyTop,
                                // display: mobDis,
                            }}
                        >
                            <button
                                type="button"
                                onClick={() => requestSort('gp')}
                                className={getClassNamesFor('gp')}
                            >
                                GP
                            </button>
                        </th>
                        <th
                            style={{
                                position: 'sticky',
                                top: stickyTop,
                                // display: mobDis,
                            }}
                        >
                            <button
                                type="button"
                                onClick={() => requestSort('sub')}
                                className={getClassNamesFor('sub')}
                            >
                                SUB
                            </button>
                        </th>
                        <th style={{ position: 'sticky', top: stickyTop }}>
                            <button
                                type="button"
                                onClick={() => requestSort('goals')}
                                className={getClassNamesFor('goals')}
                            >
                                G
                            </button>
                        </th>
                        <th style={{ position: 'sticky', top: stickyTop }}>
                            <button
                                type="button"
                                onClick={() => requestSort('assists')}
                                className={getClassNamesFor('assists')}
                            >
                                A
                            </button>
                        </th>
                        <th
                            style={{
                                position: 'sticky',
                                top: stickyTop,
                                display: mobDis,
                            }}
                        >
                            <button
                                type="button"
                                onClick={() => requestSort('cs')}
                                className={getClassNamesFor('cs')}
                            >
                                CS
                            </button>
                        </th>

                        <th
                            style={{
                                position: 'sticky',
                                top: stickyTop,
                                display: mobDis,
                            }}
                        >
                            <button
                                type="button"
                                onClick={() => requestSort('gc')}
                                className={getClassNamesFor('gc')}
                            >
                                GC
                            </button>
                        </th>
                        <th
                            style={{
                                position: 'sticky',
                                top: stickyTop,
                                display: mobDis,
                            }}
                        >
                            <button
                                type="button"
                                onClick={() => requestSort('pc')}
                                className={getClassNamesFor('pc')}
                            >
                                PC
                            </button>
                        </th>
                        <th
                            style={{
                                position: 'sticky',
                                top: stickyTop,
                                display: mobDis,
                            }}
                        >
                            <button
                                type="button"
                                onClick={() => requestSort('pm')}
                                className={getClassNamesFor('pm')}
                            >
                                PM
                            </button>
                        </th>
                        <th
                            style={{
                                position: 'sticky',
                                top: stickyTop,
                                display: mobDis,
                            }}
                        >
                            <button
                                type="button"
                                onClick={() => requestSort('ps')}
                                className={getClassNamesFor('ps')}
                            >
                                PS
                            </button>
                        </th>

                        <th style={{ position: 'sticky', top: stickyTop }}>
                            <button
                                type="button"
                                onClick={() => requestSort('og')}
                                className={getClassNamesFor('og')}
                            >
                                OG
                            </button>
                        </th>
                        <th style={{ position: 'sticky', top: stickyTop }}>
                            <button
                                type="button"
                                onClick={() => requestSort('rc')}
                                className={getClassNamesFor('rc')}
                            >
                                RC
                            </button>
                        </th>
                        <th style={{ position: 'sticky', top: stickyTop }}>
                            <button
                                type="button"
                                onClick={() => requestSort('ob')}
                                className={getClassNamesFor('ob')}
                            >
                                OB
                            </button>
                        </th>
                        <th style={{ position: 'sticky', top: stickyTop }}>
                            <button
                                type="button"
                                onClick={() => requestSort('fantasy')}
                                className={getClassNamesFor('fantasy')}
                            >
                                FP
                            </button>
                        </th>
                        <th style={{ position: 'sticky', top: stickyTop }}>
                            <button
                                type="button"
                                onClick={() => requestSort('avg')}
                                className={getClassNamesFor('avg')}
                            >
                                AVG
                            </button>
                        </th>
                    </tr>
                </thead>
                <tbody>
                    {items.map((item, i) => (
                        <tr
                            onClick={() => handleClick(item)}
                            key={`${item.id}-${i}`}
                        >
                            <td
                                style={
                                    sortConfig && sortConfig.key === 'name'
                                        ? { background: sortBG }
                                        : {}
                                }
                            >
                                {item.name}
                            </td>

                            <td
                                style={
                                    sortConfig && sortConfig.key === 'pos'
                                        ? {
                                              display: mobDis,
                                              background: sortBG,
                                          }
                                        : { display: mobDis }
                                }
                            >
                                {item.pos}
                            </td>
                            <td
                                style={
                                    sortConfig && sortConfig.key === 'gp'
                                        ? {
                                              background: sortBG,
                                          }
                                        : {}
                                }
                            >
                                {item.gp}
                            </td>
                            <td
                                style={
                                    sortConfig && sortConfig.key === 'sub'
                                        ? {
                                              background: sortBG,
                                          }
                                        : {}
                                }
                            >
                                {item.sub}
                            </td>
                            <td
                                style={
                                    sortConfig && sortConfig.key === 'goals'
                                        ? { background: sortBG }
                                        : {}
                                }
                            >
                                {item.goals || 0}
                            </td>
                            <td
                                style={
                                    sortConfig && sortConfig.key === 'assists'
                                        ? { background: sortBG }
                                        : {}
                                }
                            >
                                {item.assists || 0}
                            </td>

                            <td
                                style={
                                    sortConfig && sortConfig.key === 'cs'
                                        ? {
                                              display: mobDis,
                                              background: sortBG,
                                          }
                                        : { display: mobDis }
                                }
                            >
                                {item.cs}
                            </td>

                            <td
                                style={
                                    sortConfig && sortConfig.key === 'gc'
                                        ? {
                                              display: mobDis,
                                              background: sortBG,
                                          }
                                        : { display: mobDis }
                                }
                            >
                                {item.gc}
                            </td>
                            <td
                                style={
                                    sortConfig && sortConfig.key === 'pc'
                                        ? {
                                              display: mobDis,
                                              background: sortBG,
                                          }
                                        : { display: mobDis }
                                }
                            >
                                {item.pc}
                            </td>
                            <td
                                style={
                                    sortConfig && sortConfig.key === 'pm'
                                        ? {
                                              display: mobDis,
                                              background: sortBG,
                                          }
                                        : { display: mobDis }
                                }
                            >
                                {item.pm}
                            </td>
                            <td
                                style={
                                    sortConfig && sortConfig.key === 'shutouts'
                                        ? {
                                              display: mobDis,
                                              background: sortBG,
                                          }
                                        : { display: mobDis }
                                }
                            >
                                {item.ps}
                            </td>

                            <td
                                style={
                                    sortConfig && sortConfig.key === 'og'
                                        ? { background: sortBG }
                                        : {}
                                }
                            >
                                {item.og || 0}
                            </td>
                            <td
                                style={
                                    sortConfig && sortConfig.key === 'rc'
                                        ? { background: sortBG }
                                        : {}
                                }
                            >
                                {item.rc || 0}
                            </td>
                            <td
                                style={
                                    sortConfig && sortConfig.key === 'ob'
                                        ? { background: sortBG }
                                        : {}
                                }
                            >
                                {item.ob || 0}
                            </td>
                            <td
                                style={
                                    sortConfig && sortConfig.key === 'fantasy'
                                        ? { background: sortBG }
                                        : {}
                                }
                            >
                                {item.fantasy || 0}
                            </td>
                            <td
                                style={
                                    sortConfig && sortConfig.key === 'avg'
                                        ? { background: sortBG }
                                        : {}
                                }
                            >
                                {isNaN(item.avg) ? 0 : item.avg}
                            </td>
                        </tr>
                    ))}
                </tbody>
            </table>
        </>
    );
};

