import React from 'react';

import { useRecoilState } from 'recoil';
import {
    teamInfo,
    playerTeamsCalc,
    currentPage,
    playersRemaining,
} from './atoms';

import { useHistory } from 'react-router-dom';

export default function TeamCard(props) {
    const history = useHistory();

    const { team, canClick } = props;
    const [, setCurrentPage] = useRecoilState(currentPage);
    const [pR] = useRecoilState(playersRemaining);
    const noMargin = props.noMargin || false;
    const [LteamInfo] = useRecoilState(teamInfo);
    // const [LplayerTeams] = useRecoilState(playerTeams);
    const [scores] = useRecoilState(playerTeamsCalc);

    const [width, setWidth] = React.useState(window.innerWidth);
    function handleWindowSizeChange() {
        setWidth(window.innerWidth);
    }
    React.useEffect(() => {
        if (isNaN(scores[team].FP)) {
            // window.location.reload();
        }
        window.addEventListener('resize', handleWindowSizeChange);
        return () => {
            window.removeEventListener('resize', handleWindowSizeChange);
        };
    }, [history, scores, team]);

    function handleClick() {
        if (!canClick) return null;
        window.scrollTo(0, 0);
        switch (LteamInfo[team].teamName) {
            case 'Beavers Of Doom':
                setCurrentPage('CBD');
                break;
            case 'Pensacola Pandas':
                setCurrentPage('PEN');
                break;
            case 'Glasgow Celtics':
                setCurrentPage('GLA');
                break;
            case `Suter's Last Hurrah`:
                setCurrentPage('SUT');
                break;
            default:
                break;
        }
    }

    let isMobile = width <= 768;
    return (
        <div
            className={
                canClick
                    ? 'team-header top-border team-header-hover'
                    : 'team-header top-border'
            }
            style={noMargin ? { marginTop: '0' } : null}
            onClick={handleClick}
        >
            <img src={LteamInfo[team].userImage} alt="logo" />
            <div>
                <h1 style={isMobile ? { fontSize: '19px' } : {}}>
                    {LteamInfo[team].teamName}
                    <span
                        style={{
                            fontSize: '11px',
                            position: 'relative',
                            bottom: '3px',
                            left: '5px',
                        }}
                    >
                        {' '}
                        ({pR[team]})
                    </span>
                </h1>
                <h3 style={{ fontWeight: 'bold' }}>
                    {scores[team].FP + scores[team].BP}
                </h3>
                <div className="FPandBP">
                    <h4>FP: {scores[team].FP}</h4>
                    <h4>BP: {scores[team].BP}</h4>
                </div>
            </div>
        </div>
    );
}
