import React, { useEffect } from 'react';

import { db } from '../ContactForm/firebase';

import { useRecoilState } from 'recoil';
import { news } from './atoms';

import { Divider } from 'antd';

export default function News() {
	const [lNews, setNews] = useRecoilState(news);

	const [isSet, setIsSet] = React.useState(false);

	useEffect(() => {
		if (isSet) return;
		var docRef = db.collection('News').doc('News');

		docRef
			.get()
			.then(doc => {
				if (doc.exists) {
					// console.log('Document data:', doc.data());
					setNews(doc.data());
					setIsSet(true);
				} else {
					// doc.data() will be undefined in this case
					// console.log('No such news document!');
				}
			})
			.catch(error => {
				// console.log('Error getting news document:', error);
			});
	}, [isSet, setNews]);

	return (
		<div>
			<h1>News</h1>
			{lNews.news.map((item, i) => {
				return (
					<div style={{ paddingTop: '0' }} key={i}>
						<h4>{item}</h4>
						<Divider style={{ paddingTop: '2px' }} />
					</div>
				);
			})}
		</div>
	);
}
