import React, { useState, useRef } from 'react';
import styled from 'styled-components';

import Confetti from 'react-confetti';

import GLA8 from './GLAlogo8.png';
import CBD8 from './CANlogo8.png';
import SUT8 from './SUTlogo8.png';
import PEN8 from './PENlogo8.png';

import ReactTypingEffect from 'react-typing-effect';

export const AwardShow = props => {
	const headRef = useRef();

	const { awards } = props;

	console.log(awards);
	// if (awards)
	// 	awards.unshift({
	// 		awardName: 'Welcome to the 2021 ICHL Awards Presentation',
	// 	});

	const [index, setIndex] = useState(-1); //-1

	const [awardStage, setAwardStage] = useState(0);
	const [showButton, setShowButton] = useState(false);

	const renderWinners = () => {
		const item = awards[index];
		// return awards.map((item, i) => {
		const { numNoms } = item;
		// console.log(item);

		let tempMax = 0;
		for (let x = 0; x < numNoms; x++) {
			if (item[`vote${x}`] > tempMax) tempMax = item[`vote${x}`];
		}
		// console.log({ tempMax });
		let winners = [];
		for (let x = 0; x < numNoms; x++) {
			if (item[`vote${x}`] === tempMax)
				winners.push([item[`desc${x}`], item[`nomNum${x}`]]);
		}

		// console.log(winners);

		const CBDComp = () => {
			return (
				<div className="team-wrap">
					<div className="team-logo">
						<img src={CBD8} alt="logo" />
					</div>
					<div className="team">
						<div className="team-name CBD">
							<h2>BEAVERS OF DOOM</h2>
						</div>
					</div>
				</div>
			);
		};
		const GLAComp = () => {
			return (
				<div className="team-wrap">
					<div className="team-logo">
						<img src={GLA8} alt="logo" />
					</div>
					<div className="team">
						<div className="team-name GLA">
							<h2>GLASGOW CELTICS</h2>
						</div>
					</div>
				</div>
			);
		};

		const PENComp = () => {
			return (
				<div className="team-wrap">
					<div className="team-logo">
						<img src={PEN8} alt="logo" />
					</div>
					<div className="team">
						<div className="team-name PEN">
							<h2>PENSACOLA PANDAS</h2>
						</div>
					</div>
				</div>
			);
		};
		const SUTComp = () => {
			return (
				<div className="team-wrap">
					<div className="team-logo">
						<img src={SUT8} alt="logo" />
					</div>
					<div className="team">
						<div className="team-name SUT">
							<h2>SUTER'S LAST HURRAH</h2>
						</div>
					</div>
				</div>
			);
		};
		const teamLogo = t => {
			switch (t) {
				case 'CBD':
					return CBDComp();
				case 'PEN':
					return PENComp();
				case 'SUT':
					return SUTComp();
				case 'GLA':
					return GLAComp();
				default:
					break;
			}
		};
		const WinnerComp = winner => {
			const logo = teamLogo(winner[1]);
			// console.log(logo);
			return (
				<div className="winner">
					{logo}
					<p>{winner[0]}</p>
				</div>
			);
		};
		return (
			<div>
				<h1 className="winner-title">{item.awardName}</h1>
				<div className="winners-wrap">
					{WinnerComp(winners[0])}
					{winners[1] ? WinnerComp(winners[1]) : null}
					{winners[2] ? WinnerComp(winners[2]) : null}
					{winners[3] ? WinnerComp(winners[3]) : null}
				</div>
			</div>
		);
		// });
	};

	const renderAwards = () => {
		const current = awards[index];
		const numNoms = current.numNoms;

		console.log(current);
		let noms = [];
		for (let x = 0; x < numNoms; x++) {
			const thisNom = current[`nomNum${x}`];
			const thisDesc = current[`desc${x}`];
			noms.push([thisNom, thisDesc]);
		}

		console.log({ noms });

		if (awardStage === 0) {
			return (
				<>
					<h1>{awards[index].awardName} </h1>
					<h2>Nominees</h2>
					{noms.map((item, ind) => {
						return (
							<div
								style={{
									width: '60vw',
									marginBottom: '50px',
									marginTop: '50px',
								}}
								className={item[0]}
							>
								<h3>{item[0]}</h3>
								<h4>{item[1]}</h4>
							</div>
						);
					})}
					<button
						onClick={() => {
							headRef.current.style.opacity = '0';
							window.setTimeout(() => {
								window.scrollTo(0, 0);
								headRef.current.style.opacity = '1';
								setAwardStage(1);
							}, 1000);
						}}
					>
						See Winner
					</button>
				</>
			);
		} else {
			return (
				<>
					{/* <h1>{awards[index].awardName} </h1> */}
					<h1>
						<span role="img" aria-label="trophy">
							🏆
						</span>
						{'        '}
						WINNER
						{'        '}
						<span role="img" aria-label="trophy">
							🏆
						</span>
					</h1>
					{renderWinners()}
					<button
						onClick={() => {
							if (index < awards.length - 1) {
								headRef.current.style.opacity = '0';

								window.setTimeout(() => {
									window.scrollTo(0, 0);
									headRef.current.style.opacity = '1';
									setIndex(index + 1);
									setAwardStage(0);
								}, 1000);
							} else {
								headRef.current.style.opacity = '0';
								window.setTimeout(() => {
									window.scrollTo(0, 0);
									headRef.current.style.opacity = '1';
									setIndex(-99);
								}, 1000);
							}
						}}
					>
						Next Award
					</button>
				</>
			);
		}
	};

	const renderIntro = () => {
		console.log('passed 💩');

		let textA = 'Welcome to the 2021 ICHL Awards';
		window.setTimeout(() => {
			console.log('done writing');
			setShowButton(true);
		}, 200 * textA.length + 500);
		return (
			<>
				<Confetti />
				<h1>
					<ReactTypingEffect
						speed={200}
						typingDelay={0}
						cursor="🏒"
						eraseDelay={99999999999999999}
						text={textA}
					/>
				</h1>
				<button
					style={
						showButton ? { visibility: 'unset' } : { visibility: 'hidden' }
					}
					onClick={() => {
						setShowButton(false);
						headRef.current.style.opacity = '0';

						window.setTimeout(() => {
							window.scrollTo(0, 0);
							setIndex(0);
							headRef.current.style.opacity = '1';
							// setAwardStage(1);
						}, 1000);
					}}
				>
					NEXT
				</button>
			</>
		);
	};

	if (!awards) return <h1>Loading</h1>;
	if (index === -99)
		return (
			<div
				style={{
					height: '100vh',
					display: 'flex',
					width: '100vw',
					alignItems: 'center',
					justifyContent: 'center',
				}}
			>
				<Confetti />
				<h1
					style={{
						color: 'white',
						position: 'absolute',
						top: '0',
						fontSize: '4rem',
					}}
				>
					<span role="img" aria-label="trophy">
						🏆
					</span>
					{'        '}
					Champion!!!
					{'        '}
					<span role="img" aria-label="trophy">
						🏆
					</span>
				</h1>
				<div className="team-wrap">
					<div className="team-logo">
						<img src={GLA8} alt="logo" />
					</div>
					<div className="team">
						<div className="team-name GLA">
							<h2>GLASGOW CELTICS</h2>
						</div>
					</div>
				</div>
			</div>
		);
	else
		return (
			<AwWrap ref={headRef}>
				<Confetti />
				{index === -1 ? renderIntro() : renderAwards()}
			</AwWrap>
		);
};

const AwWrap = styled.div`
	z-index: 1000000;
	background: radial-gradient(black, transparent);

	color: white;
	min-height: 100vh;
	width: 100vw;
	display: flex;
	align-items: center;
	justify-content: space-evenly;
	flex-direction: column;

	button {
		color: black;
	}

	* {
		max-width: 95vw;
		text-align: center;
		color: white;
	}
`;
