import { Menu, Dropdown } from 'antd';
import { DownOutlined } from '@ant-design/icons';

import React from 'react';

import { teams } from './Data/teams';

import { useRecoilState } from 'recoil';
import { teamSelectedInDropDown } from './atoms';

export default function TeamsDropDown() {
	const [
		localTeamSelectedInDropDown,
		setTeamSelectedInDropDown,
	] = useRecoilState(teamSelectedInDropDown);

	function renderTeams() {
	
		return teams.teams.map((item, i) => {
			return (
				<Menu.Item
					onClick={() => setTeamSelectedInDropDown([item.name, item.id])}
					key={i}
				>
					{item.name}
				</Menu.Item>
			);
		})
}
	const menu = (
		<Menu style={{ height: '15rem', overflow: 'scroll' }}>
			{renderTeams()}
		</Menu>
	);

	return (
		<div className="team-box">
			<Dropdown overlay={menu} trigger={['click']}>
				<span
					className="ant-dropdown-link box-span"
					onClick={e => e.preventDefault()}
				>
					{localTeamSelectedInDropDown[0] || 'Team'} <DownOutlined />
				</span>
			</Dropdown>
		</div>
	);
}
