import React from 'react';
import { SortableTable } from './SortableTable';

import { useRecoilState } from 'recoil';
import { user, finalTeamPlayers } from './atoms';

import ShowTeam from './ShowTeam';

export default function MyTeamFan() {
    const [Luser] = useRecoilState(user);
    const [LfinalTeamPlayers] = useRecoilState(finalTeamPlayers);

    return (
        <div
            style={{
                // marginTop: '3rem',
                // padding: '24px',
                width: '99vw',
                background: '#edeef0',
            }}
        >
            <SortableTable
                team={Luser}
                type="display"
                products={LfinalTeamPlayers[Luser]}
            />

            <div
                id="MyTeamFan-ShowTeam-Wrap"
                style={{ display: 'none', marginTop: '3rem' }}
            >
                <ShowTeam team="CBD" teamCode="CBD" />
                <ShowTeam team="PEN" teamCode="PEN" />
                <ShowTeam team="SUT" teamCode="SUT" />
                <ShowTeam team="GLA" teamCode="GLA" />
            </div>
        </div>
    );
}
