import React, { useRef, useState, useEffect } from 'react';

import styled from 'styled-components';

import GoHome from '../GoHome';

export default function ContactFormThank(
	props = { name: 'Joe Biden', email: 'biden@win.com' }
) {
	const { name, email } = props;

	const thankYouRef = useRef();

	const [secs, setSecs] = useState(7);

	useEffect(() => {
		thankYouRef.current.style.opacity = 1;
	});

	useEffect(() => {
		window.setTimeout(() => {
			if (secs > 0) {
				setSecs(secs - 1);
			} else {
				window.location = '/';
			}
		}, 1000);
	});
	return (
		<>
			<GoHome />
			<CFT>
				{' '}
				<div ref={thankYouRef} className="thank-you">
					<h1>Thank you for reaching out, {name}</h1>
					<h2>I will contact you soon via e-mail at {email}</h2>
					<h3>Heading back in {secs}</h3>
				</div>
			</CFT>
		</>
	);
}

const CFT = styled.div`
	background: black;
	color: dodgerblue;
	height: 100vh;
	display: flex;
	transition: all 3s;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	font-family: 'Rubik', sans-serif;
	div {
		opacity: 0;
		transition: all 3s;
		height: 50vh;
		max-width: 70vw;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: space-evenly;
	}
`;
