import React, { useEffect, useState } from 'react';

import styled from 'styled-components';

import Button from '../Button';

import { Link } from 'react-router-dom';

import { Mail } from '@styled-icons/entypo/Mail';

import { Twitter } from '@styled-icons/fa-brands/Twitter';

import { Linkedin } from '@styled-icons/fa-brands/Linkedin';

import { Github } from '@styled-icons/evaicons-solid/Github';

export default function Contact() {
	const [width, setWidth] = useState(window.innerWidth);
	function handleWindowSizeChange() {
		setWidth(window.innerWidth);
	}
	useEffect(() => {
		window.addEventListener('resize', handleWindowSizeChange);
		return () => {
			window.removeEventListener('resize', handleWindowSizeChange);
		};
	}, []);
	let isMobile = width <= 768;

	return (
		<ContactWrap>
			<div className="container" id="Contact">
				<div className="left-side-contact">
					<h1 style={{ color: '#a9a9a9' }}>Let's work together</h1>
					<h3>Have a project?</h3>
					<Link target="_blank" to="contact">
						<Button maxWidth="15rem" label="contact me" />
					</Link>
				</div>
				<div className="right-side-contact">
					<span className="contact-link">
						<a
							rel="noopener noreferrer"
							target="_blank"
							href="https://www.linkedin.com/in/rick-gove"
						>
							<Linkedin
								className={isMobile ? 'mobile-icon' : 'icon'}
								size="20"
								title="email"
							/>
							{!isMobile ? '        On LinkedIn' : ''}
						</a>
					</span>
					<span className="contact-link">
						<a
							rel="noopener noreferrer"
							target="_blank"
							href="https://www.github.com/rickgove"
						>
							<Github
								className={isMobile ? 'mobile-icon' : 'icon'}
								size="20"
								title="email"
							/>
							{!isMobile ? '        On GitHub' : ''}
						</a>
					</span>
					<span className="contact-link">
						<a
							rel="noopener noreferrer"
							target="_blank"
							href="mailto:rickbgove@gmail.com"
						>
							<Mail
								className={isMobile ? 'mobile-icon' : 'icon'}
								size="20"
								title="email"
							/>
							{!isMobile ? '        On Gmail' : ''}
						</a>
					</span>
					<span className="contact-link">
						<a
							rel="noopener noreferrer"
							target="_blank"
							href="https://www.twitter.com/gove_rick"
						>
							<Twitter
								className={isMobile ? 'mobile-icon' : 'icon'}
								size="20"
								title="email"
							/>
							{!isMobile ? '        On Twitter' : ''}
						</a>
					</span>
				</div>
			</div>
		</ContactWrap>
	);
}

const ContactWrap = styled.div`
	* {
		font-family: 'Rubik', sans-serif;
	}

	a {
		text-decoration: none;
		color: inherit;
	}

	.container {
		display: flex;
		width: 100%;
		justify-content: space-evenly;
	}
	margin-top: -1px;
	display: flex;
	justify-content: center;

	clip-path: polygon(
		0 0,
		/* left top */ 100% 0,
		/* right top */ 100% 100%,
		/* right bottom */ 0 90% /* left bottom */
	);
	padding-bottom: 3rem;
	height: 20rem;
	background: black;
	color: white;
	display: flex;

	.left-side-contact {
		display: flex;
		flex-direction: column;
		justify-content: space-evenly;
		padding: 1rem 0.7rem;
		text-transform: capitalize;
		text-align: center;
		width: 50%;
		align-items: center;
	}

	h3 {
		color: #ababab;
	}

	.right-side-contact {
		display: flex;
		flex-direction: column;
		justify-content: space-evenly;
		padding: 1rem 0.7rem;
		text-transform: uppercase;

		span {
			vertical-align: center;
		}

		@media (min-width: 768px) {
			width: 30%;
		}
	}

	.icon {
		color: dodgerblue;
	}

	.mobile-icon {
		padding-right: 1rem;
		height: 3rem;
		width: 3rem;
		font-size: 2rem;
		color: dodgerblue;
	}

	.contact-link {
		cursor: pointer;
		transition: all 400ms;

		:hover {
			opacity: 0.5;
			transform: scale(1.3);
			transition: all 400ms;
		}
	}
`;
