import React from 'react';

import TeamCard from './TeamCard';
import { SortableTable } from './SortableTable';

import { euroTeams } from './atoms';
import { useRecoilState } from 'recoil';

export default function ShowTeam(props) {
    const { team } = props;

    const [teams] = useRecoilState(euroTeams);

    let teamData = [];
    teams.forEach(item => {
        if (item.shortName === team) teamData = item;
    });

    return (
        <div id="ShowTeam-InnerWrap">
            <TeamCard team={teamData} noMargin={true} />
            <SortableTable
                team={team}
                type="display"
                products={teamData.tableData}
            />
        </div>
    );
}
